import { expression_value, internal_method_info_type } from './expression_value_types';
import clone_unlinked_expression_value from './expression_value_converter';



export interface internal_method_value_with_id<caller_type extends expression_value> extends expression_value {
    type: "internal_method",
    is_internal: true,
    value: internal_method_info_type<caller_type>,
    // value: internal_method_function_type<caller_type>,
}




export default class ScopeVariable {
    id: number;
    name: string;
    value: expression_value;

    constructor(id: number, identifier_string: string, value: expression_value) {
        this.name = identifier_string;
        this.value = value;
        this.id = id;
    }

    clone_unlinked(): ScopeVariable {
        const cloned_variable = new ScopeVariable(
            this.id,
            this.name,
            clone_unlinked_expression_value(this.value)
        );
        return cloned_variable;
    }
}

