import ReactDOMServer from 'react-dom/server';
import { get_short_representation_element } from "../get_representation_levels";
import { is_array_value, expression_value, array_expression_value, object_expression_value, is_object_value, is_function_value, function_value } from "../../code_editors/js/interpreter/parsing_files/expression_value_types";
import tippy from 'tippy.js';
import 'tippy.js/themes/light.css';
import 'tippy.js/dist/tippy.css';
import ast_expand_struct from "../ast_expand_struct_types";
import eye from "./expand_icons/eye";
import ASTComponent from '../../code_editors/js/ast_components/ASTComponent';

function create_expanded_arrayexpval_element(val: array_expression_value, z_index: number) {
    const expanded_value = document.createElement("div");
    expanded_value.classList.add("expanded-array");
    expanded_value.style.zIndex = z_index.toString();
    for (let i = 0; i < val.value.length; i++) {
        const exp_val = val.value[i];
        const key_element = document.createElement("div");
        key_element.innerHTML = i.toString();
        key_element.classList.add("key-element");
        key_element.classList.add("number-key");
        const value_element = create_ast_expand_struct(exp_val).element;
        value_element.classList.add("value-element");
        const item_element = document.createElement("div");
        item_element.classList.add("item-element");
        item_element.classList.add("item-element-" + i);
        item_element.appendChild(key_element);
        item_element.appendChild(value_element);
        expanded_value.appendChild(item_element);
    }
    return expanded_value;
}

function create_expanded_objectexpval_element(val: object_expression_value, z_index: number) {
    const expanded_value = document.createElement("div");
    expanded_value.classList.add("expanded-oject");
    expanded_value.style.zIndex = z_index.toString();
    const keys = Object.keys(val.value);
    for (let i = 0; i < keys.length; i++) {
        const key = keys[i];
        const exp_val = val.value[key];
        const key_element = document.createElement("div");
        key_element.innerHTML = key;
        key_element.classList.add("key-element");
        key_element.classList.add("string-key");
        const value_element = create_ast_expand_struct(exp_val).element;
        value_element.classList.add("value-element");
        const item_element = document.createElement("div");
        item_element.classList.add("item-element");
        item_element.classList.add("item-element-" + key);
        item_element.appendChild(key_element);
        item_element.appendChild(value_element);
        expanded_value.appendChild(item_element);
    }
    return expanded_value;
}

function create_expanded_functionexpval_element(val: function_value, z_index: number) {
    const expanded_value = document.createElement("div");
    expanded_value.classList.add("expanded-function");
    const pre_wrapper = document.createElement("pre");
    pre_wrapper.classList.add("code");
    expanded_value.appendChild(pre_wrapper);
    const ast_function = val.value.function;
    // console.log({ast_function});
    const function_component_html_string = ReactDOMServer.renderToString(
        ASTComponent({ ast_object: ast_function })
    );
    // console.log({function_component_html_string});
    pre_wrapper.innerHTML = function_component_html_string;
    return expanded_value;
}

function create_expanded_value(val: expression_value, z_index: number) {
    const expanded_wrapper = document.createElement("div");
    expanded_wrapper.classList.add("expanded-value");
    expanded_wrapper.classList.add("nice-scroll-bar");
    if (is_array_value(val))
        expanded_wrapper.appendChild(
            create_expanded_arrayexpval_element(val, z_index)
        );
    else if (is_object_value(val))
        expanded_wrapper.appendChild(
            create_expanded_objectexpval_element(val, z_index)
        );
    else if (is_function_value(val))
        expanded_wrapper.appendChild(
            create_expanded_functionexpval_element(val, z_index)
        );
    else {
        const error_element = document.createElement("div");
        error_element.innerHTML = "not expandable expression value";
        return error_element;
    }

    return expanded_wrapper;
}

export default function create_ast_expand_struct(val: expression_value, z_index: number = 0): ast_expand_struct {
    const scope_value = document.createElement("span");
    scope_value.classList.add("scope-value");
    scope_value.classList.add("scope-value-" + val.id);
    const var_value = val;
    if (var_value)
        scope_value.appendChild(get_short_representation_element(var_value));
    else
        scope_value.innerHTML = "undefined???";

    let triggers = null;

    if (is_array_value(var_value) || is_object_value(var_value) || is_function_value(var_value)) {
        const expand_trigger = document.createElement("div");
        expand_trigger.innerHTML = eye;
        expand_trigger.classList.add("value-expand-trigger");
        scope_value.appendChild(expand_trigger);
        const expanded_element = create_expanded_value(var_value, z_index + 1);
        scope_value.classList.add("collapsed");
        const tippy_instance = tippy(scope_value, {
            content: expanded_element,
            trigger: "manual",
            placement: "bottom",
            theme: 'expanded-value',
            arrow: true,
            // hideOnClick: false,
            interactive: true,
            appendTo:document.body
            // popperOptions: {
            //     positionFixed: true,
            // }
        });
        let expand = function () {
            scope_value.classList.remove("collapsed");
            scope_value.classList.add("expanded");
            tippy_instance.show();
        }
        let collapse = function () {
            scope_value.classList.add("collapsed");
            scope_value.classList.remove("expanded");
            tippy_instance.hide();
        }
        expand_trigger.addEventListener("click", () => {
            expand();
        });

        triggers = {
            expand,
            collapse,
            expanded_element
        }

        // scope_value.appendChild(expanded_value);
    }

    return {
        triggers,
        element: scope_value
    }
}