import create_scope from '../animation_effects/create_scope';
import fade_in from '../animation_effects/fade_in';
import remove_scope from '../animation_effects/remove_scope';
import vertical_collapse from '../animation_effects/vertical_collapse';
import vertical_expand from '../animation_effects/vertical_expand';
import { animation_state } from '../animation_state_types';
import step_creator_type from './step_creator_type';

const new_scope_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {

    async function animate(speed: number) {
        // const current_scope = scope_element.getElementsByClassName("current-scope")[0];
        // await vertical_collapse(current_scope, speed);
        const new_scope = create_scope(scope_element);
        await vertical_expand(new_scope, speed);
    }

    async function rollback(speed: number) {
        remove_scope(scope_element);
    }

    return {
        name: "new scope",
        animate,
        rollback
    }

}

export default new_scope_step_creator;