import React from 'react';
// import { AST, ast_child_type } from '../interpreter/parsing_files/ast_objects';
// import IgnoredTokens from './ast_general_functions/IgnoredTokens';
import TokenComponent from './TokenComponent';

type ast_component_props_type = {
    ast_object: any
}

function ASTComponent(props: ast_component_props_type) {

    let child_counter = 0;

    function get_child_component(ast_child: any) {
        if(!ast_child) return null;
        const child_id = ast_child.id ? ast_child.id : child_counter++;
        if (ast_child.types)
            return <ASTComponent ast_object={ast_child} key={child_id} />;
        if (ast_child.value)
            return <TokenComponent token={ast_child} key={child_id} />

        if(Array.isArray( ast_child)){
            const ChildComponents: any = [];
            console.warn("array_child:", ast_child);
            for (let i = 0; i < ast_child.length; i++) {
                const child = ast_child[i];
                ChildComponents.push(
                    get_child_component(child)
                );
            }
            return ChildComponents;
        }

        return "!";
    }

    function render_component() {
        const object = props.ast_object;
        const children = object.children;
        const ChildComponents: any = [];
        if (children.length > 0) {
            for (let i = 0; i < children.length; i++) {
                const child = children[i];
                ChildComponents.push(
                    get_child_component(child)
                );
            }
        }
        return ChildComponents;
    }

    const classnames = [];
    try {

        classnames.push("ast-object-" + props.ast_object.id);
    } catch (error) {
        console.error("catched props of ASTComponent", props);
    }
    for (let i = 0; i < props.ast_object.types.length; i++) {
        const type = props.ast_object.types[i];
        classnames.push(type);
    }

    return (
        <span key={props.ast_object.id} className={classnames.join(" ")}>
            {render_component()}
        </span>
    );
}

export default ASTComponent;