import animation_step_type from './animation_step_type';
import { executer_options } from '../code_editors/js/interpreter/ast_executer';
import ScopeManager from '../code_editors/js/interpreter/parsing_files/scope_objects';
import { expression_value } from '../code_editors/js/interpreter/parsing_files/expression_value_types';
import { nearley_custom_object, statement_type, expression_type, for_statement_type, access_by_index_expression_type, function_definition_type, get_related_ast_ids, is_block, expression_call_type, program_type, is_binary_expression, is_function_definition, is_constant_expression } from '../code_editors/js/interpreter/ast_types';
import { animation_state, state_change_argument, evaluation_change_info_type, for_loop_restoration_change_info_type, expval_replacements_dict_type, add_to_scope_info_type, new_scope_info_type, before_function_call_info_type, state_change_info_type, assignment_info_type, index_evaluation_info_type, value_update_info_type, program_start_change_info_type, execution_start_change_info_type, scope_end_info_type, scope_change_info_type, after_function_retake_change_info_type } from './animation_state_types'
import create_steps_from_states from './create_steps_from_states';
import ScopeVariable from '../code_editors/js/interpreter/parsing_files/variable';
import create_ast_expand_struct from './animation_effects/create_ast_expand_struct';

// import { Map, List } from 'immutable';

export default class AnimationStepsCreator {

    code_scroller: Element;
    code_container: Element;
    scope_container: Element;
    original_contents_dictionary: { [key: string]: string };
    unfiltered_animation_steps: (animation_step_type | null)[];
    animations_enabled_flag: boolean;
    ids_to_skip_replacement: number[];
    highlighted_ids_before_call: number[];
    // ast_expand_triggers: ast_expand_triggers_dict_type;
    animation_states: animation_state[];

    constructor(code_wrapper: Element, scope_container: Element) {
        this.code_scroller = code_wrapper;
        this.code_container = code_wrapper.getElementsByClassName("code")[0] as Element;
        this.scope_container = scope_container;
        this.original_contents_dictionary = {};
        this.unfiltered_animation_steps = [];
        this.animations_enabled_flag = true;
        this.ids_to_skip_replacement = [];
        this.highlighted_ids_before_call = [];
        // this.ast_expand_triggers = {};
        const initial_state: animation_state = {
            ast_program: null,
            last_state_change: null,
            scope: null,
            expression_value_replacements: {},
            highlighted_ast_ids: []
        };
        this.animation_states = [
            initial_state
        ];
    }



    skip_replacement(ast_id: number) {
        if (!this.ids_to_skip_replacement.includes(ast_id))
            this.ids_to_skip_replacement.push(ast_id);
    }

    push_state(change_info: state_change_info_type, state_changes: state_change_argument = {}) {
        const new_state: animation_state = { ...this.get_last_state(), last_state_change: change_info, ...state_changes };
        this.animation_states.push(new_state);
    }

    get_last_state(): animation_state {
        return this.animation_states.slice(-1)[0];
    }

    remove_replacements(ast_ids: number[]) {
        const previous_replacements = this.get_last_state().expression_value_replacements;
        const new_ast_replacements = { ...previous_replacements };
        for (const id of ast_ids) {
            delete new_ast_replacements[id];
        }
        // console.log({ previous_replacements, new_ast_replacements, removed_ids: ast_ids });
        return new_ast_replacements;
    }

    mix_replacements(new_replacements: expval_replacements_dict_type): expval_replacements_dict_type {
        const old_replacements = this.get_last_state().expression_value_replacements;
        const mixed_replacements = { ...old_replacements, ...new_replacements };
        return mixed_replacements;
    }

    push_step(step: animation_step_type | null) {
        if (this.animations_enabled_flag)
            this.unfiltered_animation_steps.push(step);
    }

    get_ast_component(ast_id: number) {
        const element = this.code_container.getElementsByClassName("ast-object-" + ast_id)[0];
        return element as HTMLSpanElement;
    }


    save_original_content(ast_object: nearley_custom_object) {
        if (ast_object.id !== undefined) {
            const id_string = ast_object.id;
            if (!(id_string in this.original_contents_dictionary))
                this.original_contents_dictionary[id_string] = this.get_ast_component(ast_object.id).innerHTML;
        }
    }

    /* -------------------------------- callbacks ------------------------------- */


    on_program_start(ast_program: program_type, scope: ScopeManager) {
        const state_change: program_start_change_info_type = {
            change_type: "program_start",
            details: {}
        }

        const state_changes: state_change_argument = {
            ast_program
        }

        this.push_state(state_change, state_changes);
    }

    on_before_execution(ast_statement: statement_type, scope: ScopeManager) {
        this.save_original_content(ast_statement);

        const state_change: execution_start_change_info_type = {
            change_type: "execution_start",
            details: {
                ast_statement
            }
        }

        const state_changes: state_change_argument = {
            highlighted_ast_ids: [ast_statement.id]
        }

        this.push_state(state_change, state_changes);
    }

    on_after_execution(ast_statement: statement_type, scope: ScopeManager) {
        // const after_block_step = create_after_block_execution_step(ast_statement, this.scope_container);
        // this.push_step(after_block_step);
    }

    on_before_evaluation(ast_expression: expression_type, scope: ScopeManager) {
        this.save_original_content(ast_expression);
        // const after_declaration_step = create_after_declaration_step(this.code_container, ast_expression, scope, this.scope_container);
        // this.push_step(after_declaration_step);
    }

    on_after_evaluation(ast_expression: expression_type, scope: ScopeManager, returned_value: expression_value) {

        if (is_binary_expression(ast_expression) && (ast_expression.operator.value === "=")) return;
        if (is_function_definition(ast_expression)) return;
        if (is_constant_expression(ast_expression)) return;
        if (returned_value.is_internal) return;
        const ast_id = ast_expression.id;
        const state_change: evaluation_change_info_type = {
            change_type: "evaluation",
            details: {
                evaluated_ast: ast_expression,
                returned_value
            }
        }

        const state_changes: state_change_argument = {
            scope: scope,
            expression_value_replacements: this.mix_replacements({
                [ast_id]: {
                    expression_value: returned_value,
                    expand_struct: create_ast_expand_struct(returned_value)
                }
            })
        }

        this.push_state(state_change, state_changes);

    }

    on_before_for_loop(for_object: for_statement_type) {
        const change_info: for_loop_restoration_change_info_type = {
            change_type: "for loop restoration",
            details: {
                for_ast: for_object,
            }
        }

        const new_state: state_change_argument = {
            expression_value_replacements: this.remove_replacements(get_related_ast_ids(for_object)),
        }

        this.push_state(change_info, new_state);
    }

    on_variable_declaration(ast_origin: nearley_custom_object, variable: ScopeVariable, scope_manager: ScopeManager) {
        const state_change: add_to_scope_info_type = {
            change_type: "add_to_scope",
            details: {
                variable,
                ast_origin
            }
        }

        const new_state: state_change_argument = {
            scope: scope_manager,
        }

        this.push_state(state_change, new_state);
    }

    on_assignment(ast_origin: nearley_custom_object, variable: ScopeVariable, scope_manager: ScopeManager) {
        // console.log("assignment");
        const state_change: assignment_info_type = {
            change_type: "assignment",
            details: {
                variable,
                ast_origin
            }
        }

        const new_state: state_change_argument = {
            scope: scope_manager,
        }

        this.push_state(state_change, new_state);

    }

    on_index_evaluation(ast_expression: access_by_index_expression_type, index_value: string) {
        const state_change: index_evaluation_info_type = {
            change_type: "index_evaluation",
            details: {
                access_by_index_expression: ast_expression,
                index_value
            }
        }
        this.push_state(state_change);
    }

    on_value_update(exp_val: expression_value) {
        const state_change: value_update_info_type = {
            change_type: "value_update",
            details: {
                expression_value: exp_val,
            }
        }
        this.push_state(state_change);
    }

    on_after_if_condition_eval() {

    }

    on_before_function_call(function_definition: function_definition_type, function_call_expression: expression_call_type, evaluated_parameters: expression_value[]) {
        const state_change: before_function_call_info_type = {
            change_type: "before_function_call",
            details: {
                function: function_definition,
                expression_call: function_call_expression,
                parameter_values: evaluated_parameters
            }
        }

        this.highlighted_ids_before_call = [...this.get_last_state().highlighted_ast_ids];
        const old_replacements = this.get_last_state().expression_value_replacements;
        const ids_to_remove = get_related_ast_ids(function_definition);
        const new_replacements = this.remove_replacements(ids_to_remove);
        console.log({ old_replacements, ids_to_remove, new_replacements })
        const new_state: state_change_argument = {
            expression_value_replacements: new_replacements,
        }

        this.push_state(state_change, new_state);
    }

    on_function_call(function_definition: function_definition_type) {
        const state_change: after_function_retake_change_info_type = {
            change_type: "after_function_retake",
            details: {}
        }

        const state_changes: state_change_argument = {
            highlighted_ast_ids: this.highlighted_ids_before_call
        }

        this.push_state(state_change, state_changes);
        // const step = create_after_function_call_restore_step(this.code_scroller, this.original_contents_dictionary, function_definition);
        // this.push_step(step);
    }

    on_new_scope(scope: ScopeManager) {
        const state_change: new_scope_info_type = {
            change_type: "new_scope",
            details: {}
        }

        const new_state: state_change_argument = {
            scope,
        }

        this.push_state(state_change, new_state);
    }


    on_scope_end(scope: ScopeManager) {
        const state_change: scope_end_info_type = {
            change_type: "scope_end",
            details: {}
        }

        const new_state: state_change_argument = {
            scope,
        }

        this.push_state(state_change, new_state);
    }

    on_scope_change(scope: ScopeManager) {
        const state_change: scope_change_info_type = {
            change_type: "scope_change",
            details: {}
        }

        const new_state: state_change_argument = {
            scope,
        }

        this.push_state(state_change, new_state);
    }

    get_callbacks_dictionary(): executer_options {
        return {
            on_program_start: this.on_program_start.bind(this),
            on_before_execution: this.on_before_execution.bind(this),
            on_after_execution: this.on_after_execution.bind(this),
            on_before_evaluation: this.on_before_evaluation.bind(this),
            on_after_evaluation: this.on_after_evaluation.bind(this),
            on_before_for_loop: this.on_before_for_loop.bind(this),
            on_variable_declaration: this.on_variable_declaration.bind(this),
            on_after_if_condition_eval: this.on_after_if_condition_eval.bind(this),
            on_assignment: this.on_assignment.bind(this),
            on_index_evaluation: this.on_index_evaluation.bind(this),
            on_value_update: this.on_value_update.bind(this),
            on_function_call: this.on_function_call.bind(this),
            on_before_function_call: this.on_before_function_call.bind(this),
            on_new_scope: this.on_new_scope.bind(this),
            on_scope_end: this.on_scope_end.bind(this),
            on_scope_change: this.on_scope_change.bind(this),
        }
    }



    get_animation_steps(): animation_step_type[] {
        return create_steps_from_states(this.animation_states, this.code_scroller, this.scope_container);
        // return this.unfiltered_animation_steps.filter(s => s) as animation_step_type[];
    }
}