import React, { useState, useEffect, useRef } from 'react';
import TokenBasedCode from './TokenBasedCode';
import ASTBasedCode from './ASTBasedCode';
import { program_type } from '../interpreter/ast_types';


type code_renderer_props = {
    code: string,
    on_code_change: (c: string) => void,
    program_status?: program_type | null;
}

export default function CodeRenderer(props: code_renderer_props) {
    const ast_program = props.program_status;
    let code_ref = React.createRef<HTMLDivElement>();
    useEffect(() => {
        const code_element = code_ref.current;
        if(code_element){

            const url_emts = code_element.getElementsByClassName("cm-url");
            for (let i = 0; i < url_emts.length; i++) {
                const url = url_emts[i];
                const url_str = url.innerHTML;
                url.innerHTML = `<a href="${url_str}">${url_str}</a>`;
            }
        }
    });

    function CodeSelector() {
        if (!ast_program) {
            return (
                <TokenBasedCode 
                on_code_change = {props.on_code_change}
                code={props.code} 
                />
            )
        } else {
            return (
                <ASTBasedCode ast_program={ast_program} />
            )
        }
    }

    if (!ast_program) {
        console.log("rendering with tokens");
    } else {
        console.log("rendering with ast");
    }

    return (
        <div ref={code_ref} className="height-100">
            <CodeSelector />
        </div>
    )
}

