import React, { useRef, useState } from 'react';
import Button from 'react-bootstrap/Button';
import { ReactComponent as BackwardIcon } from './animation_controls_icons/backward.svg';
import { ReactComponent as PreviousIcon } from './animation_controls_icons/skip_previous.svg';
import { ReactComponent as NextIcon } from './animation_controls_icons/skip_next.svg';
import { ReactComponent as PlayIcon } from './animation_controls_icons/play_arrow.svg';
import { ReactComponent as PauseIcon } from './animation_controls_icons/pause.svg';
import { ReactComponent as SlowIcon } from './animation_controls_icons/slow.svg';
import { ReactComponent as FastIcon } from './animation_controls_icons/fast.svg';
import './input_range.css';

export type playback_state = "playing" | "playing_backwards" | "playing_step_forward" | "playing_step_backwards" | "paused"

export interface playback_state_change_ref {
    current: (s: playback_state) => void
};

type props_type = {
    on_playback_state_change_ref: playback_state_change_ref,
    rollback_all_handler: () => void,
    play_handler: () => void,
    pause_handler: () => void,
    step_forward_handler: () => void,
    step_backward_handler: () => void,
    speed_change_handler: (speed: number) => void,
    default_speed: number,
}



function AnimationControls(props: props_type) {

    const [playbackState, setPlaybackState] = useState<playback_state>("paused");
    props.on_playback_state_change_ref.current = (s: playback_state) => {
        setPlaybackState(s);
    }

    function set_speed_handler(event: React.ChangeEvent<HTMLInputElement>) {
        const speed = parseFloat(event.target.value);
        props.speed_change_handler(speed);
    }

    return (
        <div className="controls-container show">
            <div className="form-group text-light"
            >
                <label className="text-light">
                    Playback Controls
                </label>
                <div className="input-group btn-group-vertical playback-buttons">

                    <Button disabled={playbackState === "paused" ? false : true} variant="secondary"
                        onClick={props.rollback_all_handler}
                    >
                        <BackwardIcon />
                    </Button>

                    <Button disabled={playbackState === "paused" ? false : true} variant="secondary"
                        onClick={props.step_backward_handler}
                    >
                        <PreviousIcon />
                    </Button>
                    <Button disabled={playbackState === "paused" ? false : true} variant="secondary"
                        onClick={props.step_forward_handler}
                    >
                        <NextIcon />
                    </Button>
                    <Button disabled={(playbackState === "playing" || playbackState === "playing_backwards") ? false : true} variant="secondary"
                        onClick={props.pause_handler}
                    >
                        <PauseIcon />
                    </Button>
                    <Button disabled={playbackState === "paused" ? false : true} variant="secondary"
                        onClick={props.play_handler}
                    >
                        <PlayIcon />
                    </Button>
                </div >

                <div className="speed-control  py-4">
                    <div className="speed-icon" ><SlowIcon /></div>
                    <input style={{ direction: "rtl" }} type="range" name="animation-speed" min="0" max="500" step="100" defaultValue={props.default_speed} onChange={set_speed_handler} id="" />
                    <div className="speed-icon" ><FastIcon /></div>
                </div>
            </div >
        </div >
    );
};

export default AnimationControls;