import gsap from 'gsap';

type view_blocker_data_type = {
    top: number,
    left: number,
    width: number,
    height: number,
}

function get_view_blocker_data(inline_element: Element, scrollable_container: Element): view_blocker_data_type {
    const container_rect = scrollable_container.getBoundingClientRect();
    const inline_rect = inline_element.getBoundingClientRect();
    return {
        top: inline_rect.top - container_rect.top,
        left: inline_rect.left - container_rect.left,
        width: inline_rect.width,
        height: inline_rect.height,
    };
}


export default function set_replacement(scrollable_container: Element, element_to_replace: Element, new_content: Element, speed: number) {

    return new Promise((resolve) => {

        element_to_replace.innerHTML = element_to_replace.innerHTML; // get rid of all references;
        const old_vb_data = get_view_blocker_data(element_to_replace, scrollable_container);
        // console.log({scrollable_container});
        // console.log({old_vb_data});
        const view_blocker = document.createElement('span');
        view_blocker.classList.add("view-blocker");
        scrollable_container.appendChild(view_blocker);
        const tl = gsap.timeline();

        tl.set(view_blocker, { ...old_vb_data, opacity: 0 });
        tl.to(view_blocker, {
            duration: speed / 2000, opacity: 1, onComplete: () => {
                const re = <HTMLDivElement>element_to_replace;
                re.style.opacity="0";
                const finaltl = gsap.timeline({
                    onComplete: () => {
                        view_blocker.remove();
                        resolve();
                    }
                });

                let new_vb_data: view_blocker_data_type | null = null;
                const original_innerhtml = element_to_replace.innerHTML;
                element_to_replace.innerHTML = "";
                const nc = <HTMLDivElement>new_content.cloneNode(true);
                nc.style.opacity = "0";
                element_to_replace.after(nc);
                new_vb_data = get_view_blocker_data(nc, scrollable_container);
                nc.remove();
                element_to_replace.innerHTML = original_innerhtml;
                
                finaltl.to(view_blocker, {
                    duration: speed / 2000, ...new_vb_data ? new_vb_data : {}, onComplete: () => {
                        element_to_replace.innerHTML = "";
                        element_to_replace.after(new_content);
                        element_to_replace.remove();
                    }
                });
                finaltl.to(view_blocker, { duration: speed / 2000, opacity: 0 });
            }
        });
    })
}