import gsap from 'gsap';
import { ScrollToPlugin } from 'gsap/all';
gsap.registerPlugin(ScrollToPlugin);

export default function scroll_to(element: Element, container: Element, speed: number) {
    const el = element as HTMLElement;
    const cont = container as HTMLElement;
    let elementTop = el.offsetTop;
    let container_top = cont.offsetTop;
    let elementRelativeTop = elementTop - container_top - cont.clientHeight / 2;
    let scroll_needed = elementRelativeTop - cont.scrollTop;
    let duration = speed * Math.abs(scroll_needed) / 100000;
    return new Promise((resolve) => {
        const tl = gsap.timeline({ onComplete: resolve });
        tl.to(cont, { duration, scrollTo: elementRelativeTop });
    });

}