
import { startState } from 'codemirror';
import moo_lexer from '../interpreter/nearley_lexer';

export default function (config, parserConfig) {

    // Interface

    return {
        startState: function(){
            moo_lexer.reset();
            // console.log(moo_lexer.save());
            return {
                current: moo_lexer.save()
            };
        },
        token: function (stream, state) {
            // console.log(state);
            let returned = "whatever";
            try {
                if (!stream.eol()) {
                    const stream_string = stream.string.slice(stream.column());
                    moo_lexer.reset(stream_string, state.current);
                    const next_token = moo_lexer.next();
                    if (next_token) {
                        for (let i = 0; i < next_token.value.length; i++) {
                            stream.next();
                        }
                        returned = next_token.type;
                    } else {
                        stream.next();
                    }
                }
            } catch (error) {
                console.error(error);
                returned = "whatever";
            }
            state.current = moo_lexer.save();
            return returned;
        },
    };
}