import add_to_scope from '../animation_effects/add_to_scope';
import create_variable_element from '../animation_effects/create_scope_var_element';
import drag_element_to_another from '../animation_effects/drag_one_element_to_another';
import hide_scope from '../animation_effects/hide_scope';
import remove_from_scope from '../animation_effects/remove_from_scope';
import scroll_to from '../animation_effects/scroll_to';
import show_scope from '../animation_effects/show_scope';
import vertical_expand from '../animation_effects/vertical_expand';
import { add_to_scope_info_type, animation_state } from '../animation_state_types';
import animation_step_type from '../animation_step_type';
import step_creator_type from './step_creator_type';

const add_to_scope_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {
    const change_info = after.last_state_change as add_to_scope_info_type;
    const variable = change_info.details.variable;


    const step: animation_step_type = {
        name: "add to scope",
        animate: async (speed: number) => {
            const scope_var_elment = create_variable_element(variable!);
            const element_to_move_into_scope = code_element.getElementsByClassName("ast-object-" + change_info.details.ast_origin.id)[0] as Element;
            const dragged_element = document.createElement("div");
            dragged_element.classList.add("drag-to-scope");
            dragged_element.appendChild(scope_var_elment);
            const current_scope = scope_element.getElementsByClassName("current-scope")[0];
            const dummy = document.createElement('div');
            dummy.classList.add("scope-var");
            dummy.classList.add("dummy-scope-var");
            dummy.innerHTML = "&nbsp;";
            current_scope.appendChild(dummy);
            await vertical_expand(dummy, speed);
            // await show_scope(scope_element, speed);
            const scope_scroller = scope_element.getElementsByClassName("scrollable-y")[0];
            await Promise.all(
                [
                    scroll_to(element_to_move_into_scope, code_element, speed),
                    scroll_to(current_scope.lastElementChild ? current_scope.lastElementChild : current_scope, scope_scroller, speed)
                ]
            );
            await drag_element_to_another(element_to_move_into_scope, dragged_element, dummy, speed)
            dragged_element.remove();
            dummy.remove();
            await add_to_scope(scope_element, scope_var_elment);
        },
        rollback: async (speed: number) => {
            await remove_from_scope(scope_element, variable.id);
        }
    }

    return step;
    // const show_scope_step = create_show_scope_step(scope_container);
    // const hide_scope_step = create_hide_scope_step(scope_container);
    // return merge_steps([show_scope_step, step, hide_scope_step]);

}

export default add_to_scope_step_creator;
