import { animation_state, is_add_to_scope_change, is_after_function_retake_change, is_assignment_change, is_before_function_call_change, is_evaluation_change, is_execution_start_change, is_for_loop_restoration, is_index_evaluation_change, is_new_scope_change, is_scope_change_change, is_scope_end_change, is_value_update_change } from "./animation_state_types";
import animation_step_type from "./animation_step_type";
import add_to_scope_step_creator from "./step_creators/add_to_scope_step_creator";
import before_function_call_step_creator from "./step_creators/before_function_call_step_creator";
import after_evaluation_replacement_step_creator from "./step_creators/evaluation_replacement_step_creator";
import new_scope_step_creator from "./step_creators/new_scope_step_creator";
import for_loop_restoration_step_creator from "./step_creators/for_loop_restoration_step_creator";
import step_creator_type from "./step_creators/step_creator_type";
import starting_step_creator from "./step_creators/starting_step_creator";
import ending_step_creator from "./step_creators/ending_step_creator";
import assignment_step_creator from "./step_creators/assignment_step_creator";
import index_evaluation_step_creator from "./step_creators/index_evaluation_step_creator";
import value_update_step_creator from "./step_creators/value_update_step_creator";
import merge_steps from "./merge_steps";
import execution_start_step_creator from "./step_creators/execution_start_step_creator";
import scope_end_step_creator from "./step_creators/scope_end_step_creator";
import scope_change_step_creator from "./step_creators/scope_change_step_creator";
import after_function_retake_step_creator from "./step_creators/after_function_retake_step_creator";

export default function create_steps_from_states(states: animation_state[], code_element: Element, scope_element: Element): animation_step_type[] {
    let steps: (animation_step_type)[] = [];
    let last_state = states[0];

    const starting_step = starting_step_creator(
        last_state,
        last_state,
        code_element,
        scope_element
    )

    if (starting_step) {
        steps.push(starting_step);
    }

    let step_pending_to_merge: animation_step_type | null = null;


    for (const state of states) {
        let step: animation_step_type | null = null;
        let step_creator: step_creator_type | null = null;
        const state_change = state.last_state_change;
        let is_pending_step = false;
        if (state_change === null)
            continue;

        // TODO: this is better implemented with a dictionary,
        //  the only advantage of this are type guards 

        if (is_evaluation_change(state_change)) {
            step_creator = after_evaluation_replacement_step_creator;
        } else if (is_for_loop_restoration(state_change)) {
            step_creator = for_loop_restoration_step_creator;
        } else if (is_new_scope_change(state_change)) {
            step_creator = new_scope_step_creator;
        } else if (is_scope_end_change(state_change)) {
            step_creator = scope_end_step_creator;
        } else if (is_scope_change_change(state_change)) {
            step_creator = scope_change_step_creator;
        } else if (is_add_to_scope_change(state_change)) {
            step_creator = add_to_scope_step_creator;
        } else if (is_assignment_change(state_change)) {
            step_creator = assignment_step_creator;
        } else if (is_execution_start_change(state_change)) {
            step_creator = execution_start_step_creator;
        } else if (is_after_function_retake_change(state_change)) {
            step_creator = after_function_retake_step_creator;
        } else if (is_index_evaluation_change(state_change)) {
            step_creator = index_evaluation_step_creator;
            is_pending_step = true;
        } else if (is_value_update_change(state_change)) {
            step_creator = value_update_step_creator;
        } else if (is_before_function_call_change(state_change)) {
            step_creator = before_function_call_step_creator;
        }

        if (step_creator) {
            step = step_creator(
                last_state,
                state,
                code_element,
                scope_element
            );
            if (step) {
                if (is_pending_step)
                    step_pending_to_merge = step;
                else {
                    if (step_pending_to_merge) {
                        let merged_step = merge_steps([step_pending_to_merge, step]);
                        step_pending_to_merge = null;
                        if (merged_step)
                            steps.push(merged_step);
                    } else
                        steps.push(step);
                }
            }
        }

        last_state = state;
    }


    const ending_step = ending_step_creator(
        last_state,
        last_state,
        code_element,
        scope_element
    )

    if (ending_step) {
        steps.push(ending_step);
    }



    return steps;
}