import { nearley_node, declaration_statement_type, statement_type, single_declaration_type, program_type, optional_assignment_fragment_type, parameter_type, function_definition_type, expression_call_type, identifier_expression_type, constant_expression_type, array_expression_type, identifier_key_type, string_key_type, key_value_pair_type, object_expression_type, access_by_index_expression_type, property_expression_type, return_statement_type, assignment_fragment_type, break_statement_type, continue_statement_type } from './ast_types';
import { if_statement_type } from './ast_types';
import { for_statement_type } from './ast_types';
import { else_fragment_type } from './ast_types';
import { declaration_fragment_type } from './ast_types';
import { list_node_getter } from './ast_types';
import { group_expression_type } from './ast_types';
import { block_type } from './ast_types';
import { expression_statement_type } from './ast_types';
import { binary_operation_type } from './ast_types';
import { expression_type } from './ast_types';

function get_post_processors() {

    let cnt = 0;

    let dflt = (t = "wrapper") => ((d: nearley_node) => ({ id: cnt++, types: [t], children: d }));
    
    
    let block_comment_content = ((d: nearley_node[]) => {
        console.log(d);
        return { id: cnt++, types: "comment", children: d }
    });

    let mark_important = (important_index = 0) => ((d: nearley_node) => ({ id: cnt++, types: ["important_mark"], children: d, important: get_important(d, important_index) }));

    let get_important = (array: any, index: number) => {
        if (array[index] && array[index].types && array[index].types.includes("important_mark")) {
            return array[index].important;
        }
        return array[index];
    };

    let list_initialization = (d: nearley_node) => ({ id: cnt++, types: ["important_mark"], important: d, children: d })

    let list_node = (d: nearley_node) => ({ id: cnt++, types: ["list"], list: get_important(d, 0), children: d });

    let empty_list = (d: nearley_node) => ({ id: cnt++, types: ["list"], list: [], children: d });

    let append_element = (d: nearley_node) => ({
        id: cnt++, types: ["important_mark"],
        important: [get_important(d, 0),
        ...get_important(d, 2)], children: d
    });

    let program = (d: nearley_node): program_type => {

        const last = cnt++;
        const returned: program_type = {
            id: last,
            types: ["statement", "program"],
            statements_list_node: get_important(d, 0),
            children: d
        }

        // cnt = 0;
        return returned;
    };

    let binary_operation = (d: nearley_node): binary_operation_type => ({
        id: cnt++, types: ["expression", "binary_expression"],
        left: get_important(d, 0), operator: get_important(d, 2),
        right: get_important(d, 4), children: d
    })


    let block_statement = (d: nearley_node): block_type => ({ id: cnt++, types: ["statement", "block_statement"], statements_list_node: get_important(d, 1), children: d });

    let group = (d: nearley_node): group_expression_type => ({ id: cnt++, types: ["expression", "group_expression"], expression: get_important(d, 2), children: d });

    let expression_statement = (d: nearley_node): expression_statement_type => ({ id: cnt++, types: ["statement", "expression_statement"], expression: get_important(d, 0), children: d });

    let declaration_statement = (d: nearley_node): declaration_statement_type => ({ id: cnt++, types: ["statement", "declaration_statement"], declaration_fragment: get_important(d, 0), children: d });
    
    let break_statement = (d: nearley_node): break_statement_type => ({ id: cnt++, types: ["statement", "break_statement"],  children: d });
    
    let continue_statement = (d: nearley_node): continue_statement_type => ({ id: cnt++, types: ["statement", "continue_statement"],  children: d });

    let declaration_fragment = (d: nearley_node): declaration_fragment_type => ({
        id: cnt++, types: ["declaration_fragment"], declaration_keyword: get_important(d, 0), declarations_list: get_important(d, 2), children: d
    });

    let single_declaration = (d: nearley_node): single_declaration_type => ({ id: cnt++, types: ["single_declaration"], identifier: get_important(d, 0), optional_assignment_fragment: get_important(d, 1), children: d });

    let assignment_fragment = (d: nearley_node): assignment_fragment_type => ({
        id: cnt++, types: ["optional_assignment_fragment"], value: get_important(d, 3), children: d
    });

    let parameter = (d: nearley_node): parameter_type => ({
        id: cnt++, types: ["parameter"], parameter_identifier: get_important(d, 0), optional_assignment_fragment: get_important(d, 1), children: d
    });

    let forpp = (d: nearley_node): for_statement_type => ({
        id: cnt++, types: ["statement", "for_statement"],
        for_keyword: get_important(d, 0),
        for_init: get_important(d, 4),
        condition: get_important(d, 8),
        final_expression: get_important(d, 12),
        statement: get_important(d, 16),
        children: d
    });

    let ifpp = (d: nearley_node): if_statement_type => (
        {
            id: cnt++,
            types: ["statement", "if_statement"],
            if_keyword: get_important(d, 0),
            condition: get_important(d, 4),
            statement: get_important(d, 8),
            else_fragment: get_important(d, 9),
            children: d
        });

    let else_fragment = (d: nearley_node): else_fragment_type => ({
        id: cnt++,
        types: ["else_fragment"],
        else_keyword: get_important(d, 1),
        statement: get_important(d, 3),
        children: d
    })

    let function_definition = (d: nearley_node): function_definition_type => ({
        id: cnt++,
        types: ["expression", "function_definition_expression"],
        function_keyword: get_important(d, 0),
        parameters_list: get_important(d, 3),
        block_statement: get_important(d, 6),
        children: d
    })

    let expression_call = (d: nearley_node): expression_call_type => ({
        id: cnt++,
        types: ["expression", "expression_call"],
        expression: get_important(d, 0),
        call_parameters: get_important(d, 3),
        children: d
    })
    let identifier_expression = (d: nearley_node): identifier_expression_type => ({
        id: cnt++,
        types: ["expression", "identifier_expression"],
        token: get_important(d, 0),
        children: d
    });

    let constant_expression = (d: nearley_node): constant_expression_type => ({
        id: cnt++,
        types: ["expression", "constant_expression"],
        token: get_important(d, 0),
        children: d
    });

    let arraypp = (d: nearley_node): array_expression_type => ({
        id: cnt++,
        types: ["expression", "array_expression"],
        items_list: get_important(d, 1),
        children: d
    });

    let identifier_key = (d: nearley_node): identifier_key_type => ({
        id: cnt++,
        types: ["key", "idenrifier_key"],
        value: get_important(d, 0).value,
        token: get_important(d, 0),
        children: d
    });

    let string_key = (d: nearley_node): string_key_type => ({
        id: cnt++,
        types: ["key", "string_key"],
        value: get_important(d, 0).value.substr(1, get_important(d, 0).value.length - 2),
        token: get_important(d, 0),
        children: d
    });

    let key_value_pair = (d: nearley_node): key_value_pair_type => ({
        id: cnt++,
        types: ["key_value_pair"],
        key: get_important(d, 0),
        value: get_important(d, 4),
        children: d
    });

    let object = (d: nearley_node): object_expression_type => ({
        id: cnt++,
        types: ["expression", "object_expression"],
        key_value_pairs_list: get_important(d, 1),
        children: d
    });

    let property_expression = (d: nearley_node): property_expression_type => ({
        id: cnt++,
        types: ["expression", "property_expression"],
        expression: get_important(d, 0),
        property_identifier: get_important(d, 4),
        children: d
    });

    let access_by_index_expression = (d: nearley_node): access_by_index_expression_type => ({
        id: cnt++,
        types: ["expression", "access_by_index"],
        expression: get_important(d, 0),
        index: get_important(d, 4),
        children: d
    });

    let return_statement = (d: nearley_node): return_statement_type => ({
        id: cnt++,
        types: ["statement", "return_statement"],
        return_keyword: get_important(d, 0),
        expression: get_important(d, 2),
        children: d
    });

    const exported = {
        dflt,
        mark_important,
        program,
        list_initialization,
        append_element,
        binary_operation,
        block_statement,
        group,
        expressions_list: list_node as list_node_getter<expression_type>,
        statement_list: list_node as list_node_getter<statement_type>,
        declaration_list: list_node as list_node_getter<single_declaration_type>,
        parameters_list: list_node as list_node_getter<parameter_type>,
        key_value_list: list_node as list_node_getter<key_value_pair_type>,
        parameter,
        declaration_statement,
        declaration_fragment,
        assignment_fragment,
        forpp,
        ifpp,
        else_fragment,
        single_declaration,
        empty_list,
        expression_statement,
        function_definition,
        expression_call,
        identifier_expression,
        constant_expression,
        arraypp,
        identifier_key,
        string_key,
        key_value_pair,
        object,
        access_by_index_expression,
        property_expression,
        return_statement,
        break_statement,
        continue_statement,
        block_comment_content
    };

    return exported;
}


module.exports = get_post_processors;
// export default exported;