// import gsap from 'gsap';

export default function clear_highlights(code_container: Element) {
    console.log("clearing_highlights in", code_container);
    const previously_highlighted = document.getElementsByClassName("highlighted");
    while (previously_highlighted.length) {
        previously_highlighted[0].classList.remove("highlighted");
    }
    const previously_non_highlighted = document.getElementsByClassName("lowlighted");
    while (previously_non_highlighted.length) {
        previously_non_highlighted[0].classList.remove("lowlighted");
    }

}

