import { expression_value, array_expression_value, object_expression_value, basic_array_value, counter_ref } from './expression_value_types';
import get_array_members from '../internal_members';

export let default_undefined_value: expression_value = {
    id: -1899,
    type: "undefined",
    value: undefined,
};

export function get_default_array_value(cr: counter_ref) {
    let default_array_value: basic_array_value = {
        id: ++cr.current,
        type: "array",
        value: [],
    };
    const members = get_array_members(default_array_value);
    default_array_value.members = members;
    const av = default_array_value as array_expression_value;
    return av;
}

export function get_default_object_value(cr: counter_ref) {
    let default_array_value: object_expression_value = {
        id: ++cr.current,
        representation_name: "object",
        type: "object",
        value: {},
    };
    return default_array_value;
}