const moo = require("moo");

const lexer = moo.states({
    normal: {
        string: /".*?"/,
        // block_comment: { match: /\/\*(?:.|\n)*?\*\// },
        block_comment_start: { match: /\/\*/, push: "comment" },
        line_comment: { match: /\/\/.*/ },
        ws: { match: /[\s\t]+/, lineBreaks: true },
        number: /[0-9]+(?:\.[0-9]+)?/,
        true: ["true"],
        false: ["false"],
        identifier: {
            match: /[a-zA-Z_]+[0-9]*/, type: moo.keywords({
                keyword: ["for", "let", "function", "if", "while", "else", "return", "break", "continue"],
            })
        },
        binary_op: ["*", "/", "=", "+", "-", "||", "&&", "<", ">", "<=", ">=", "==", "==="],
        symbol: [".", ",", ";", "(", ")", "{", "}"],
        undef: /./
    },

    comment: {
        block_comment_end: { match: /\*\//, push: "normal" },
        // url:{match:/https/},
        // url:{match:/https?:\/\/(?:www\.)?[^/S]/},
        url:{match:/https?:\/\/(?:www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b(?:[-a-zA-Z0-9()@:%_\+.~#?&//=]*)/},
        // comment: { match: /(?:.+|(?:\n|\s|\t)+)/, lineBreaks: true },
        comment: { match: /[\S]+/, lineBreaks: true },
        ws_comment: { match: /(?:\n|\s|\t)+/, lineBreaks: true },
        undef: /./
    }

});

