import React, { ReactNode, useRef, useState } from 'react';
import AnimationControls, { playback_state_change_ref } from './AnimationControls';
import { ReactComponent as EditIcon } from './toolbar_icons/edit.svg';
import { ReactComponent as FilmIcon } from './toolbar_icons/film.svg';
type state_mode_type = "edit" | "playback";



type props_type = {
    on_playback_state_change_ref: playback_state_change_ref,
    state_mode: state_mode_type,
    toggle_mode_handler: () => void,
    rollback_all_handler: () => void,
    play_handler: () => void,
    pause_handler: () => void,
    step_forward_handler: () => void,
    step_backward_handler: () => void,
    speed_change_handler: (speed: number) => void
    default_speed: number,
}


function Toolbar(props: props_type) {

    // const [stateMode, setstateMode] = useState<state_mode_type>("edit");
    let controls: ReactNode | null = null;

    let ModeIcon = EditIcon;

    if (props.state_mode === "playback") {
        controls = (
            <AnimationControls
                on_playback_state_change_ref={props.on_playback_state_change_ref}
                play_handler={props.play_handler}
                pause_handler={props.pause_handler}
                rollback_all_handler={props.rollback_all_handler}
                step_backward_handler={props.step_backward_handler}
                step_forward_handler={props.step_forward_handler}
                speed_change_handler={props.speed_change_handler}
                default_speed={props.default_speed}
            />
        );
        ModeIcon = FilmIcon;
    }

    return (
        <div className="toolbar">
            <div className="triggers toolbar-section">
                <div className="form-group">
                    <label className="text-light">Set Mode</label>
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <div onClick={props.toggle_mode_handler} className="btn btn-secondary ">Toggle</div>
                        </div>
                        <div className="input-group-append">
                            <span className="input-group-text" >
                                <ModeIcon />
                            </span>
                        </div>
                    </div>
                </div>
                {/* <div className="input-group">
                    <div className="toolbar-button btn btn-secondary" onClick={props.toggle_mode_handler}>toggle mode</div>
                    <div className="toolbar-button btn btn-secondary" onClick={props.toggle_mode_handler}>toggle mode</div>
                </div> */}
                {/* <div className="minimize-trigger minimize-scope" onClick={toggle_scope_visibility} dangerouslySetInnerHTML={{ __html: stack_icon }} /> */}
                {/* <div className="minimize-trigger minimize-playback-controls" onClick={toggle_play_controls_visibility} dangerouslySetInnerHTML={{ __html: movie_icon }} /> */}
            </div>
            <div className="toolbar-section">
                {controls}
            </div>

        </div>
    );
};

export default Toolbar;