import { CHANGE_CODE_TYPE } from '../actions/change_code';
import { SET_SELECTION_STATUS_TYPE } from '../actions/set_selection_status';
import { SET_REPLACEMENT_TYPE } from '../actions/set_replacement';
import { CLEAR_REPLACEMENTS_TYPE } from '../actions/clear_replacements';
