const default_code = `

/*

Hi, I'm Adrian, Creator of this demo.
this is an animated code editor,
this is what comes to my mind when someone says 
they can not SEE what the code does.

Disclaimer: this is not JS, 
it's just a language that resembles js on purpose for this proof of concept.
I call it tinylang. It supports variables, functions, objects and arrays  
and is lexical scoped.

feel free to write me to my main 
site https://adriandiazcode.github.io/profile_page/ if 
you find a bug/suggestion.

Enjoy ;)

*/



let get_candidate_score = function (candidate, requirements_array) {
    let score = 0;
    let candidate_skills = candidate.skills;
    for(let i = 0; i < requirements_array.length; i = i + 1){
        let requirement = requirements_array[i];
        for(let j = 0; j < candidate_skills.length; j = j+1){
            let skill = candidate_skills[j];
            if(skill == requirement){
                score = score +10;
                break;
            }
        }
    }
    return score;
};


let get_best_candidate = function (candidates_array, requirements_array) {
    let best_candidate_so_far = 0;
    let max_score = 0;
    for (let i = 0; i < candidates_array.length; i=i+1) {
        let candidate = candidates_array[i];
        let candidate_score = get_candidate_score(candidate, requirements_array);
        if (candidate_score > max_score) {
            max_score = candidate_score;
            best_candidate_so_far = i;
        }
    }
    return candidates_array[best_candidate_so_far];
};

let first_candidate = {
    name: "Adrian",
    skills: ["react", "django", "go"]
};

let second_candidate = {
    name: "May B. Notgood",
    skills: ["can move his ear"]
};

let candidates = [
    first_candidate,
    second_candidate
];

let requirements = ["react","django"];
let best_candidate = get_best_candidate(candidates, requirements);
let best_candidate_name = best_candidate.name;










`;


const executed_ast_statements = [];
const scopes_json = [];
const replacements_dict = {};

const default_state = {
    test: ":)",
    code: default_code,
    replacements_dict,
    executed_ast_statements,
    scopes_json,
    selection_status: {
        cursor_position: null,
        cursor_selection_end: null,
    }
};

function reducer(state = default_state, action) {

    switch (action.type) {
        case CHANGE_CODE_TYPE:
            return Object.assign({}, state, {
                code: action.payload
            })
        case SET_SELECTION_STATUS_TYPE:
            let selection_status = action.payload;
            if (selection_status.cursor_selection_end < selection_status.cursor_position || selection_status.cursor_selection_end == null) {
                selection_status.cursor_selection_end = selection_status.cursor_position
            }
            return Object.assign({}, state, {
                selection_status: action.payload
            })
        case SET_REPLACEMENT_TYPE:
            let replacement_data = action.payload;
            let replacements_dict = { ...state.replacements_dict };
            replacements_dict[replacement_data.ast_object_id] = replacement_data.replacement_string;
            return Object.assign({}, state, {
                replacements_dict
            })
        case CLEAR_REPLACEMENTS_TYPE:
            return Object.assign({}, state, {
                replacements_dict: {}
            })
        default:
            return state
    }
}

export default reducer;