import ScopeManager from "../../code_editors/js/interpreter/parsing_files/scope_objects";
import scroll_to from "./scroll_to";
import gsap from 'gsap';
import search_svg_content from './variable_search_files/search';
import highlight_element from "./highlight_element";

type point = {
    x: number,
    y: number
}

function get_global_coordinate(element: Element): point {
    var rect = element.getBoundingClientRect(),
        scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
        scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    return { y: rect.top + scrollTop, x: rect.left + scrollLeft }
}

function get_relative_cordinate(element: Element, reference: Element): point {
    const element_point = get_global_coordinate(element);
    const reference_point = get_global_coordinate(reference);
    const relative_point: point = {
        x: element_point.x - reference_point.x,
        y: element_point.y - reference_point.y
    }
    return relative_point;
}

export default function search_identifier(variable_name: string, scope_manager: ScopeManager, scope_container: Element, speed: number) {

    return new Promise(async resolve => {

        const variable = scope_manager.get_variable(variable_name);
        const scrollable = scope_container.getElementsByClassName("scrollable-y")[0];
        // console.log(scrollable);
        if (variable) {
            const var_id = variable.id;
            const variable_element = scrollable.getElementsByClassName("scope-var-id-" + var_id)[0];
            await scroll_to(variable_element, scrollable, speed);
            const scope_reference_container = scrollable.getElementsByClassName("scopes-fullheight-wrapper")[0];
            // const scope_pos = get_global_coordinate(scope_reference_container);
            const container_rect = scope_reference_container.getBoundingClientRect();
            const var_pos = get_relative_cordinate(variable_element, scope_reference_container);
            const search_icon = document.createElement("div");
            search_icon.innerHTML = search_svg_content;
            search_icon.classList.add("search-icon");
            scope_reference_container.appendChild(search_icon);
            const icon_height = search_icon.getBoundingClientRect().height;
            search_icon.style.display = "none";
            // console.log({icon_height});
            // console.log({container_height:container_rect.height});
            
            gsap.set(search_icon, { y: container_rect.height - icon_height, top: 0, left: 0, opacity: 0, display: "block" })
            const tl = gsap.timeline({
                onComplete: () => {
                    highlight_element(scrollable, "lowlight");
                    highlight_element(variable_element);
                    setTimeout(() => {
                        search_icon.remove();
                        highlight_element(scrollable);
                        resolve();
                    }, speed);
                }
            });
            tl.to(search_icon, { duration: speed / 3000, opacity: 1 });
            tl.to(search_icon, { duration: speed / 1000, y: var_pos.y });
            tl.to(search_icon, { duration: speed / 3000, opacity: 0 });

        }
    });

}