import set_replacement from '../animation_effects/set_replacement';
import { is_binary_expression, is_function_definition, is_constant_expression, is_identifier_expression } from "../../code_editors/js/interpreter/ast_types";
import step_creator_type from './step_creator_type';
import { animation_state, execution_start_change_info_type, is_evaluation_change } from '../animation_state_types';
import get_element_from_ast from './get_element_from_ast';
import scroll_to from '../animation_effects/scroll_to';
import search_identifier from '../animation_effects/search_identifier_effect';
import restore_state from './restore_state';
import highlight_ast_object from '../animation_effects/highlight_ast_object';
import highlight_element from '../animation_effects/highlight_element';
import clear_highlights from '../animation_effects/clear_highlights';


const execution_start_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {

    const last_change = after.last_state_change as execution_start_change_info_type;
    const last_change_details = last_change.details;
    // const returned_value = last_change_details.returned_value;

    async function animate(speed: number) {
        const el = code_element.getElementsByClassName("ast-object-" + last_change_details.ast_statement.id)[0];
        await scroll_to(el, code_element, speed);
        await highlight_ast_object(code_element, last_change_details.ast_statement.id, speed)

    }

    async function rollback(speed: number) {
        clear_highlights(code_element);
        if (before.highlighted_ast_ids.length > 0) {
            // highlight_element(code_element, "lowlight");
            for (const id of before.highlighted_ast_ids) {
                const el = code_element.getElementsByClassName("ast-object-" + id)[0];
                await scroll_to(el, code_element, speed);
                await highlight_ast_object(code_element, id, speed);
            }
        }
    }

    return {
        name: "execution start",
        animate,
        rollback
    }

}

export default execution_start_step_creator;