import { expression_value, is_string_value, is_number_value, is_array_value, is_object_value, is_boolean_value, is_undefined_value, is_function_value } from "../code_editors/js/interpreter/parsing_files/expression_value_types";
// import { get_short_representation_element } from "./get_short_representation_element";
export function mock_token_element(content: string, token_type: string): Element {
    const string_token = document.createElement("span");
    string_token.className = "token cm-" + token_type;
    string_token.innerHTML = content;
    return string_token;
}


export function get_short_representation_element(expression_val: expression_value): Element {

    const exp = expression_val;

    if (is_boolean_value(expression_val)) {
        const boolval = expression_val.value;
        if (expression_val.value) {
            const string_token = mock_token_element(boolval, "true");
            return string_token;
        }
        else {
            const string_token = mock_token_element(boolval, "false");
            return string_token;
        }
    }

    else if (is_string_value(exp)) {
        const string_token = mock_token_element("\"" + exp.value + "\"", "string");
        return string_token;
    }

    else if (is_number_value(exp)) {
        return mock_token_element(exp.value.toString(), "number");
    }


    else if (is_array_value(expression_val)) {
        return mock_token_element("[...]", "none");
    }


    else if (is_object_value(expression_val)) {
        return mock_token_element("{...}", "none");
    }

    else if (is_undefined_value(expression_val)) {
        return mock_token_element("undefined", "none");
    }

    else if (is_function_value(exp)) {
        const function_representation_element = document.createElement("span");
        function_representation_element.appendChild(mock_token_element("&#x192;", "symbol"));
        function_representation_element.appendChild(mock_token_element("(", "symbol"));
        function_representation_element.appendChild(mock_token_element("...", "none"));
        const parameters = exp.value.function.parameters_list.list;
        // for (let i = 0; i < parameters.length; i++) {
        //     const p = parameters[i];
        //     const identifier = p.parameter_identifier.value
        //     function_representation_element.appendChild(mock_token_element(identifier, "identifier"));
        //     if (i !== parameters.length - 1){
        //         function_representation_element.appendChild(mock_token_element(", ", "symbol"));
        //     }
        // }
        function_representation_element.appendChild(mock_token_element(")", "symbol"));
        return function_representation_element;

    }



    return mock_token_element("short_non_represented", "error");
}

export function get_intermediate_representation_element(expression_val: expression_value): Element {

    if (is_string_value(expression_val)) {
        return get_short_representation_element(expression_val);
    }

    if (is_number_value(expression_val)) {
        return get_short_representation_element(expression_val);
    }

    if (is_boolean_value(expression_val)) {
        return get_short_representation_element(expression_val);
    }

    if (is_array_value(expression_val)) {
        const array_component = document.createElement("span");
        const open_braces_component = mock_token_element("[", "none");
        array_component.appendChild(open_braces_component);
        let items = expression_val!.value;
        const items_component = document.createElement("span");

        if (items.length > 0) {
            const first_item = get_short_representation_element(items[0]);
            items_component.appendChild(first_item);
            if (items.length > 1) {
                if (items.length > 2) {
                    const mid_item = mock_token_element(", ...", "symbol");
                    items_component.appendChild(mid_item);
                }
                const final_separator = document.createElement("span");
                final_separator.innerHTML = ", ";
                items_component.appendChild(final_separator);
                const final_item = get_short_representation_element(items[items.length - 1]);
                items_component.appendChild(final_item);
            }
        }
        array_component.appendChild(items_component);
        const close_braces_component = mock_token_element("]", "none");
        array_component.appendChild(close_braces_component);
        return array_component;
    }



    if (is_object_value(expression_val)) {
        const array_component = document.createElement("span");
        const open_braces_component = mock_token_element("{", "none");
        array_component.appendChild(open_braces_component);
        const keys = Object.keys(expression_val!.value);
        const dict = expression_val!.value;
        const items_component = document.createElement("span");

        if (keys.length > 0) {
            const first_key = mock_token_element("\"" + keys[0] + "\"", "string")
            const colon = mock_token_element(":", "symbol")
            const first_item = get_short_representation_element(dict[keys[0]]);
            items_component.appendChild(first_key);
            items_component.appendChild(colon);
            items_component.appendChild(first_item);
            if (keys.length > 1) {
                if (keys.length > 2) {
                    const mid_item = mock_token_element(", ...", "symbol");
                    items_component.appendChild(mid_item);
                }

                const final_separator = document.createElement("span");
                final_separator.innerHTML = ", ";
                items_component.appendChild(final_separator);
                const final_key = mock_token_element("\"" + keys[keys.length - 1] + "\"", "string")
                const final_colon = mock_token_element(":", "symbol")
                const final_item = get_short_representation_element(dict[keys[keys.length - 1]]);
                items_component.appendChild(final_key);
                items_component.appendChild(final_colon);
                items_component.appendChild(final_item);

            }
        }
        array_component.appendChild(items_component);
        const close_braces_component = mock_token_element("}", "none");
        array_component.appendChild(close_braces_component);
        return array_component;
    }

    if (is_undefined_value(expression_val)) {
        return get_short_representation_element(expression_val);
    }

    if (is_function_value(expression_val)) {
        return get_short_representation_element(expression_val);
    }

    return mock_token_element("intermediate_non_represented", "error");
}
