import get_array_members from '../internal_members';
import { expression_value, array_expression_value, object_expression_value, object_dict_type, is_array_value, is_object_value } from './expression_value_types';

export default function clone_unlinked_expression_value(exp_val: expression_value): expression_value {
    let returned: expression_value = {
        id: exp_val.id,
        type: exp_val.type,
        value: exp_val.value,
    };
    if (is_array_value(exp_val))
        returned = clone_unlinked_array(exp_val);
    else if (is_object_value(exp_val))
        returned = clone_unlinked_object(exp_val);
    return returned;
}


function clone_unlinked_array(arr: array_expression_value): array_expression_value {
    const cloned_arr: expression_value[] = [];
    const cloned_members: any = {};
    for (const item of arr.value) {
        cloned_arr.push(clone_unlinked_expression_value(item));
    }

    for (const member_key of Object.keys(arr.members)) {
        cloned_members[member_key] = get_array_members(arr);
    }
    const returned: array_expression_value = {
        id: arr.id,
        type: "array",
        value: cloned_arr,
        members: cloned_members
    }
    return returned;
}

function clone_unlinked_object(obj: object_expression_value): object_expression_value {
    const object_dict: object_dict_type = {};
    for (const key of Object.keys(obj.value)) {
        const item = obj.value[key];
        object_dict[key] = clone_unlinked_expression_value(item);
    }
    const returned: object_expression_value = {
        id: obj.id,
        type: "object",
        value: object_dict,
        members: obj.members,
        representation_name: obj.representation_name
    }
    return returned;
}