import add_or_remove_class_to_text_containers from "./add_or_remove_class_to_text_containers";

export default function highlight_element(el: Element, type: "highlight" | "lowlight" = "highlight") {
    if (type === "highlight") {
        add_or_remove_class_to_text_containers(el, "lowlighted", "remove");
        add_or_remove_class_to_text_containers(el, "highlighted");
    } else {
        add_or_remove_class_to_text_containers(el, "lowlighted");
        add_or_remove_class_to_text_containers(el, "highlighted", "remove");
    }
}