import { function_definition_type } from "../ast_types";
import ScopeManager from "./scope_objects";

export type expression_value_type = "undefined" | "null" | "number" | "string" | "boolean" | "function" | "object" | "array" | "internal_method";


export interface counter_ref {
    current: number
}

export type member_getter = (cr: counter_ref) => expression_value;

export interface expression_value {
    id: number,
    type: expression_value_type,
    value: any,
    members?: { [k: string]: member_getter }
    is_internal?: boolean,
};

// export interface expression_value_with_id extends expression_value  {
//     id: number,
//     members?: { [k: string]: expression_value_with_id }
// };

export interface internal_expression_value extends expression_value {
    type: expression_value_type,
    value: any,
    is_internal: true,
};


export type function_type = {
    function: function_definition_type,
    scope: ScopeManager
}

export interface number_value extends expression_value {
    type: "number",
    value: number,
}

export interface boolean_value extends expression_value {
    type: "boolean",
    value: boolean,
}

export interface string_value extends expression_value {
    type: "string",
    value: string,
}

export interface function_value extends expression_value {
    type: "function",
    value: function_type,
}

export type internal_method_function_type<caller_type extends expression_value> = (method_caller: caller_type, ...args: expression_value[]) => any;

export type internal_method_info_type<caller_type extends expression_value> = {
    caller: caller_type,
    method_mutates_caller: boolean,
    function: internal_method_function_type<caller_type>,
};


export interface internal_method_value<caller_type extends expression_value> extends internal_expression_value {
    type: "internal_method",
    is_internal: true,
    value: internal_method_info_type<caller_type>,
    // value: internal_method_function_type<caller_type>,
}

export type general_internal_method_value = internal_method_value<expression_value>;

export type array_members_type = {
    push: (cr: counter_ref) => internal_method_value<basic_array_value>,
    length: member_getter,
    [k: string]: member_getter
}

export interface basic_array_value extends expression_value {
    type: "array",
    value: expression_value[],
}

export interface array_expression_value extends basic_array_value {
    members: array_members_type
}

export type object_dict_type = { [key: string]: expression_value; };

export interface object_expression_value extends expression_value {
    type: "object",
    value: object_dict_type,
    representation_name: string | null
}


export function is_array_value(value: expression_value): value is array_expression_value {
    return (value.type === "array");
}
export function is_object_value(value: expression_value): value is object_expression_value {
    return (value.type === "object");
}
export function is_undefined_value(value: expression_value): value is expression_value {
    return (value.type === "undefined");
}
export function is_boolean_value(value: expression_value): value is expression_value {
    return (value.type === "boolean");
}
export function is_null_value(value: expression_value): value is expression_value {
    return (value.type === "null");
}
export function is_function_value(value: expression_value): value is function_value {
    return (value.type === "function");
}
export function is_number_value(value: expression_value): value is number_value {
    return (value.type === "number");
}
export function is_string_value(value: expression_value): value is string_value {
    return (value.type === "string");
}
export function is_internal_method(value: expression_value): value is internal_method_value<expression_value> {
    return (value.type === "internal_method");
}
