import ScopeManager from "../../code_editors/js/interpreter/parsing_files/scope_objects";

export default async function remove_from_scope(scope_container: Element, variable_id: number) {
    const current_scope = scope_container.getElementsByClassName("current-scope")[0];
    const var_to_remove = current_scope.getElementsByClassName("scope-var-id-" + variable_id)[0];
    var_to_remove.remove();
}