import step_creator_type from './step_creator_type';
import { animation_state,  index_evaluation_info_type } from '../animation_state_types';
import highlight_element from '../animation_effects/highlight_element';


const index_evaluation_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {
    const change = after.last_state_change as index_evaluation_info_type;

    function animate(speed: number) {
        // console.log({after})
        return new Promise(r => {
            const ast_expression = change.details.access_by_index_expression;
            const expand_struct = after.expression_value_replacements[ast_expression.expression.id].expand_struct.triggers;
            const index_value = change.details.index_value;
            if(expand_struct){
                expand_struct.expand();
                const property_element = expand_struct.expanded_element.getElementsByClassName("item-element-" + index_value)[0];
                setTimeout(() => {
                    highlight_element(expand_struct.expanded_element, "lowlight");
                    setTimeout(() => {
                        highlight_element(property_element);
                        setTimeout(()=>{
                            expand_struct.collapse();
                            highlight_element(expand_struct.expanded_element);
                            r();
                        }, speed / 1.5);
                    }, speed / 3);
                }, speed / 3);
            }
        });
    }

    async function rollback(speed: number) {
    }

    return {
        name: "property search",
        animate,
        rollback
    }


}

export default index_evaluation_step_creator;