import { default_undefined_value } from './default_expression_values';
import ScopeVariable from "./variable";
import { expression_value, counter_ref } from './expression_value_types';

type scoped_variables_dict_type = {
    [var_name: string]: ScopeVariable
};


class Scope {
    scoped_variables: scoped_variables_dict_type;

    constructor() {
        this.scoped_variables = {};
    }

    clone_unlinked(): Scope {
        const new_scoped_variables: scoped_variables_dict_type = {}
        const new_scope = new Scope();
        const var_keys_list = Object.keys(this.scoped_variables);
        for (const key of var_keys_list) {
            new_scoped_variables[key] = this.get_variable(key)!.clone_unlinked();
        }
        new_scope.scoped_variables = new_scoped_variables;
        return new_scope;
    }

    add_variable(variable: ScopeVariable) {
        this.scoped_variables[variable.name] = variable;
    }

    get_variable(variable_name: string) {
        if (variable_name in this.scoped_variables)
            return this.scoped_variables[variable_name];
        else
            return null
    }
}

class ScopeManager {
    scopes_list: Scope[];
    is_closure: boolean;
    variable_counter_ref: counter_ref;

    constructor() {
        this.scopes_list = [];
        this.is_closure = false;
        this.variable_counter_ref = { current: 0 };
    }

    clone_scopes_list(scopes_list: Scope[]): Scope[] {
        const new_scope_list = [];
        for (const scope of scopes_list) {
            new_scope_list.push(scope.clone_unlinked())
        }
        return new_scope_list;
    }

    clone_unlinked(): ScopeManager {
        let copied_manager = new ScopeManager();
        copied_manager.scopes_list = this.clone_scopes_list(this.scopes_list);
        copied_manager.is_closure = this.is_closure;
        copied_manager.variable_counter_ref = { ...this.variable_counter_ref }
        return copied_manager;
    }

    clone_as_closure() {
        let copied_manager = new ScopeManager();
        copied_manager.scopes_list = [...this.scopes_list];
        copied_manager.is_closure = true;
        copied_manager.variable_counter_ref = this.variable_counter_ref;
        return copied_manager;
    }

    get_scopes_json() {
        return this.scopes_list.map(
            s => {
                let scoped_variables: any = {};
                let variable_keys = Object.keys(s.scoped_variables);

                for (let i = 0; i < variable_keys.length; i++) {
                    const var_name = variable_keys[i];
                    let variable = s.get_variable(var_name);
                    if (variable)
                        scoped_variables[var_name] = {
                            name: variable.name,
                            value: variable.value,
                        };
                }
            }
        );
    }

    set_scopes_json(json: JSON) {
        // TODO: implement set_scopes_json
    }

    start_scope() {
        // console.log("new scope started")
        let new_scope = new Scope();
        this.scopes_list.push(new_scope)
        // console.log("scopes_list after new scope:", this.scopes_list);
    }

    end_scope() {
        // console.log("scope ended")
        this.scopes_list.pop();
    }

    get_current_scope() {
        if (this.scopes_list.length > 0)
            return this.scopes_list.slice(-1)[0];
        else
            return null;
    }

    add_variable(variable_name: string, value: expression_value) {

        let variable = new ScopeVariable(++this.variable_counter_ref.current, variable_name, value);
        let current_scope = this.get_current_scope();
        if (current_scope)
            current_scope.add_variable(variable);
        // console.log("adding variable: ", variable_name);
        // console.log("scopes_list: ", this.scopes_list); 
    }

    is_varname_available_in_current_scope(variable_name: string) {
        let scope = this.get_current_scope();
        if (scope !== null) {
            let vars_dict = scope.scoped_variables;
            if (variable_name in vars_dict)
                return true
        }
        return false;
    }

    get_variable(variable_name: string) {
        for (let i = this.scopes_list.length - 1; i >= 0; i--) {
            const scope = this.scopes_list[i];
            const variable = scope.get_variable(variable_name);
            if (variable)
                return variable;
        }
        return null;
    }

    set_variable(variable_name: string, value: expression_value) {
        let variable = null;
        for (let i = this.scopes_list.length - 1; i >= 0; i--) {
            const scope = this.scopes_list[i];
            variable = scope.get_variable(variable_name);
            if (variable)
                break;
        }
        if (!variable) {
            console.log(this.scopes_list);
            console.log(variable_name);
            console.log("variable ", variable_name, " not found in scope");
            throw Error("variable " + variable_name + " not found in scope");
        }
        variable.value = value;
    }




}

export default ScopeManager;