import React, { useContext } from 'react'
import { UnControlled as CodeMirror } from 'react-codemirror2';
import tinylang from './tinylang';
import 'codemirror/lib/codemirror.css';
// import 'codemirror/theme/material.css';
require('codemirror/mode/javascript/javascript');

const lexer = require('../interpreter/nearley_lexer');
type props_type = {
    code: string,
    on_code_change: (c: string) => void,
}

export default function TokenBasedCode(props: props_type) {

    return (
        <CodeMirror
            value={props.code}
            options={{
                mode: 'tinylang',
                // theme: 'material',
                lineNumbers: true
            }}
            defineMode={{
                name: "tinylang",
                fn: tinylang
            }}
            // editorWillUnmount={(editor)=>{

            // }}
            onChange={(editor, data, value) => {
                // console.log({ value });
                props.on_code_change(value);
            }}
        />
    )

}
