export default function add_or_remove_class_to_text_containers(element: Element, classname: string = "lowlighted", operation_type: "add" | "remove" = "add") {
    let children = element.children;
    if(element instanceof SVGElement){
        if (operation_type === "add")
            element.parentElement?.classList.add(classname);
        else
            element.parentElement?.classList.remove(classname);
        return;
    }
    if (children.length === 0) {
        if (operation_type === "add")
            element.classList.add(classname);
        else
            element.classList.remove(classname);
    } else {
        for (let i = 0; i < children.length; i++) {
            const child = children[i];
            add_or_remove_class_to_text_containers(child, classname, operation_type);
        }
    }
}