import { expression_value, array_members_type, number_value, internal_method_value, internal_method_function_type, basic_array_value, counter_ref } from "./parsing_files/expression_value_types";
const get_push_method = (returned_val_id: number) => {

    let push_method: internal_method_function_type<basic_array_value> = function (arr_ev: basic_array_value, ev: expression_value) {
        const new_length = arr_ev.value.push(ev);
        const returned: number_value = {
            id: returned_val_id,
            type: "number",
            value: new_length
        }
        return returned;
    };
    return push_method;
}


export function get_array_members(arr_exp_val: basic_array_value) {

    const push_method_getter = (cr: counter_ref): internal_method_value<basic_array_value> => {
        return {
            id: ++cr.current,
            type: "internal_method",
            value: {
                caller: arr_exp_val,
                function: get_push_method(++cr.current),
                method_mutates_caller: true
            },
            is_internal: true,
        }
    }

    const length_getter = (cr: counter_ref): number_value => (
        {
            id: ++cr.current,
            value: arr_exp_val.value.length,
            type: "number",
            // is_internal: true,
        }
    )

    const array_internal_members: array_members_type = {
        push: push_method_getter,
        length: length_getter
    };

    return array_internal_members;
}

export default get_array_members;
