import React, { useContext } from 'react';
import CharComponent from './CharComponent';
import { token_type } from '../interpreter/ast_types';

type props_type = {
    token: token_type,
}

export default function TokenComponent(props: props_type) {

    const token = props.token;
    let token_classes = ["token"];
    token_classes.push("cm-" + token.type);

    return (<span className={token_classes.join(" ")}>{props.token.value}</span>)
}