export interface nearley_custom_object { id: number, types: string[], children: nearley_node };
export type nearley_node = nearley_custom_object[];
export type token_type = { type: string, value: string };
export interface expression_type extends nearley_custom_object {
    id: number,
    types: ["expression", ...string[]]

};
export interface statement_type extends nearley_custom_object {
    id: number,
    types: ["statement", ...string[]]
};

export interface if_statement_type extends statement_type {
    if_keyword: token_type,
    condition: expression_type,
    statement: statement_type,
    else_fragment: else_fragment_type | null,
};

export interface else_fragment_type extends nearley_custom_object {
    else_keyword: token_type,
    statement: statement_type,
};

export interface list_node_type<T> extends nearley_custom_object {
    list: T[],
};

export type list_node_getter<T> = (d: nearley_node) => list_node_type<T>;

export interface for_statement_type extends statement_type {
    for_keyword: token_type,
    for_init: for_init_type,
    condition: expression_type,
    final_expression: expression_type,
    statement: statement_type,
};

type for_init_type = declaration_fragment_type | expression_type | null

function is_forinit_expression(finit: for_init_type): finit is expression_type {
    if (finit) {
        if (finit.types.includes("expression"))
            return true;
    }
    return false;
};
function is_forinit_declaration(finit: for_init_type): finit is declaration_fragment_type {
    if (finit !== null) {
        if (finit.types.includes("declaration_fragment"))
            return true;
    }
    return false;
};

export interface declaration_statement_type extends statement_type {
    types:["statement","declaration_statement"],
    declaration_fragment: declaration_fragment_type,
};

export interface break_statement_type extends statement_type {
    types:["statement","break_statement"],
};
export interface continue_statement_type extends statement_type {
    types:["statement","continue_statement"],
};

export interface expression_statement_type extends statement_type {
    expression: expression_type,
};

export interface group_expression_type extends expression_type {
    expression: expression_type,
};

export interface array_expression_type extends expression_type {
    items_list: list_node_type<expression_type>,
    types: ["expression", "array_expression"],
};

export interface key_type extends nearley_custom_object {
    types: ["key", ...string[]],
    value: string,
};

export interface identifier_key_type extends key_type {
    types: ["key", "idenrifier_key"],
    token: token_type
};

export interface string_key_type extends key_type {
    types: ["key", "string_key"],
    token: token_type
};

export interface key_value_pair_type extends nearley_custom_object {
    types: ["key_value_pair"],
    key: key_type,
    value: expression_type
};

export interface object_expression_type extends expression_type {
    types: ["expression", "object_expression"],
    key_value_pairs_list: list_node_type<key_value_pair_type>
};

export interface property_expression_type extends expression_type {
    types: ["expression", "property_expression"],
    expression: expression_type,
    property_identifier: token_type,
};

export interface identifier_expression_type extends expression_type {
    token: token_type,
};

export interface constant_expression_type extends expression_type {
    token: token_type,
};

export interface declaration_fragment_type extends nearley_custom_object {
    declaration_keyword: token_type,
    declarations_list: list_node_type<single_declaration_type>,
};

export interface single_declaration_type extends nearley_custom_object {
    identifier: token_type,
    optional_assignment_fragment: optional_assignment_fragment_type,
};


export interface block_type extends statement_type {
    statements_list_node: list_node_type<statement_type>,
};

export interface return_statement_type extends statement_type {
    return_keyword: token_type,
    expression: expression_type,
    types: ["statement", "return_statement"],
};

export interface binary_operation_type extends expression_type {
    left: expression_type,
    right: expression_type,
    operator: token_type
}

export interface program_type extends statement_type {
    statements_list_node: list_node_type<statement_type>,
    types: ["statement", "program"]
};

export interface assignment_fragment_type extends nearley_custom_object {
    value: expression_type,
};

export type optional_assignment_fragment_type = assignment_fragment_type | null;


type parameter_identifier_type = token_type;

export interface parameter_type extends nearley_custom_object {
    parameter_identifier: parameter_identifier_type,
    optional_assignment_fragment: optional_assignment_fragment_type

}

export interface function_definition_type extends expression_type {
    function_keyword: token_type,
    parameters_list: list_node_type<parameter_type>,
    block_statement: block_type
}

export interface expression_call_type extends expression_type {
    expression: expression_type,
    call_parameters: list_node_type<expression_type>,
}

export interface access_by_index_expression_type extends expression_type {
    expression: expression_type,
    types: ["expression", "access_by_index"],
    index: expression_type,
}

export function is_block(statement: statement_type): statement is block_type {
    return statement.types.includes("block_statement");
}

export function is_declaration_statement(statement: statement_type): statement is declaration_statement_type {
    return statement.types.includes("declaration_statement");
}

export function is_break_statement(statement: statement_type): statement is break_statement_type {
    return statement.types.includes("break_statement");
}

export function is_continue_statement(statement: statement_type): statement is continue_statement_type {
    return statement.types.includes("continue_statement");
}

export function is_if_statement(statement: statement_type): statement is if_statement_type {
    return statement.types.includes("if_statement");
}

export function is_for_statement(statement: statement_type): statement is for_statement_type {
    return statement.types.includes("for_statement");
}

export function is_expression(obj: nearley_custom_object): obj is expression_type {
    return obj.types.includes("expression");
}

export function is_statement(obj: nearley_custom_object): obj is statement_type {
    return obj.types.includes("statement");
}

export function is_declaration_fragment(obj: nearley_custom_object): obj is declaration_fragment_type {
    return obj.types.includes("declaration_fragment");
}

export function is_expression_statement(statement: statement_type): statement is expression_statement_type {
    return statement.types.includes("expression_statement");
}

export function is_binary_expression(expression: expression_type): expression is binary_operation_type {
    return expression.types.includes("binary_expression");
}

export function is_group_expression(expression: expression_type): expression is group_expression_type {
    return expression.types.includes("group_expression");
}
export function is_identifier_expression(expression: expression_type): expression is identifier_expression_type {
    return expression.types.includes("identifier_expression");
}
export function is_constant_expression(expression: expression_type): expression is constant_expression_type {
    return expression.types.includes("constant_expression");
}

export function is_function_definition(expression: expression_type): expression is function_definition_type {
    return expression.types.includes("function_definition_expression");
}

export function is_expression_call(expression: expression_type): expression is expression_call_type {
    return expression.types.includes("expression_call");
}

export function is_array_expression(expression: expression_type): expression is array_expression_type {
    return expression.types.includes("array_expression");
}

export function is_object_expression(expression: expression_type): expression is object_expression_type {
    return expression.types.includes("object_expression");
}

export function is_property_expression(expression: expression_type): expression is property_expression_type {
    return expression.types.includes("property_expression");
}

export function is_access_by_index_expression(expression: expression_type): expression is access_by_index_expression_type {
    return expression.types.includes("access_by_index");
}

export function is_return_statement(expression: statement_type): expression is return_statement_type {
    return expression.types.includes("return_statement");
}

export type ast_dependency_tree = {
    ast_id: number,
    children: ast_dependency_tree[];
}


// TODO: This is just too fragile, I think this could be better implemented using the nearley children
export function get_ast_dependency_tree(ast_obj: nearley_custom_object): ast_dependency_tree {
    let children: ast_dependency_tree[] = [];
    let ast_type_not_found = true;
    if (is_expression(ast_obj)) {
        ast_type_not_found = false;
        if (is_array_expression(ast_obj)) {
            for (const exp of ast_obj.items_list.list) {
                const child_ast_tree = get_ast_dependency_tree(exp);
                children.push(child_ast_tree);
            }
        } else if (is_binary_expression(ast_obj)) {
            const left_dt = get_ast_dependency_tree(ast_obj.left);
            const right_dt = get_ast_dependency_tree(ast_obj.right);
            children.push(left_dt);
            children.push(right_dt);
        } else if (is_group_expression(ast_obj)) {
            children.push(get_ast_dependency_tree(ast_obj.expression));
        } else if (is_object_expression(ast_obj)) {
            for (const key_val of ast_obj.key_value_pairs_list.list) {
                const exp_dt = get_ast_dependency_tree(key_val.value);
                children.push(exp_dt);
            }
        } else if (is_property_expression(ast_obj)) {
            children.push(get_ast_dependency_tree(ast_obj.expression));
        } else if (is_access_by_index_expression(ast_obj)) {
            children.push(get_ast_dependency_tree(ast_obj.expression));
            children.push(get_ast_dependency_tree(ast_obj.index));
        } else if (is_function_definition(ast_obj)) {
            children.push(get_ast_dependency_tree(ast_obj.block_statement));
            for (const ast of ast_obj.parameters_list.list) {
                if (ast.optional_assignment_fragment)
                    children.push(get_ast_dependency_tree(ast.optional_assignment_fragment));
            }
        } else if (is_expression_call(ast_obj)) {
            children.push(get_ast_dependency_tree(ast_obj.expression));
            for (const expression of ast_obj.call_parameters.list) {
                children.push(get_ast_dependency_tree(expression));
            }
        } else if (is_constant_expression(ast_obj) || is_identifier_expression(ast_obj)) {

        } else {
            throw new Error("expression of types " + ast_obj.types.join(" ") + " is not included in dependency_tree function");
        }
    }
    if (is_statement(ast_obj)) {
        ast_type_not_found = false;
        if (is_if_statement(ast_obj)) {
            const condition_dt = get_ast_dependency_tree(ast_obj.condition);
            const statement_dt = get_ast_dependency_tree(ast_obj.statement);
            children.push(condition_dt, statement_dt);
            if (ast_obj.else_fragment) {
                const else_dt = get_ast_dependency_tree(ast_obj.else_fragment.statement);
                children.push(else_dt);
            }
        }
        else if (is_for_statement(ast_obj)) {
            let for_init = ast_obj.for_init;
            if (is_forinit_expression(for_init)) {
                children.push(get_ast_dependency_tree(for_init));
            } if (is_forinit_declaration(for_init)) {
                for (const declaration of for_init.declarations_list.list) {
                    if (declaration.optional_assignment_fragment) {
                        let assignment_expression = declaration.optional_assignment_fragment.value;
                        const assdt = get_ast_dependency_tree(assignment_expression);
                        children.push(assdt);
                    }
                }
            }
            children.push(get_ast_dependency_tree(ast_obj.condition));
            children.push(get_ast_dependency_tree(ast_obj.final_expression));
        } else if (is_return_statement(ast_obj)) {
            children.push(get_ast_dependency_tree(ast_obj.expression));
        } else if (is_expression_statement(ast_obj)) {
            children.push(get_ast_dependency_tree(ast_obj.expression));
        } else if (is_declaration_statement(ast_obj)) {
            for (const declaration of ast_obj.declaration_fragment.declarations_list.list) {
                if (declaration.optional_assignment_fragment) {
                    let assignment_expression = declaration.optional_assignment_fragment.value;
                    const assdt = get_ast_dependency_tree(assignment_expression);
                    children.push(assdt);
                }
            }
        } else if (is_block(ast_obj)) {
            for (const statement of ast_obj.statements_list_node.list) {
                children.push(get_ast_dependency_tree(statement));
            }
        } else {
            throw new Error("statement of types " + ast_obj.types.join(" ") + " is not included in dependency_tree function");
        }
    } if (ast_type_not_found) {
        throw new Error("ast object of types " + ast_obj!.types.join(" ") + " is not included in dependency_tree function");
    }

    return {
        ast_id: ast_obj.id,
        children
    }
}

function get_ast_dependency_array(ast_tree: ast_dependency_tree): number[] {
    let array_of_related_ast_ids = [ast_tree.ast_id];
    if (ast_tree.children.length > 0) {
        for (const child_tree of ast_tree.children) {
            array_of_related_ast_ids.push(...get_ast_dependency_array(child_tree))
        }
    }
    return array_of_related_ast_ids;
}

export function get_related_ast_ids(ast_obj: nearley_custom_object) {
    const ast_tree = get_ast_dependency_tree(ast_obj);
    return get_ast_dependency_array(ast_tree);
}

export default nearley_node;




