import vertical_collapse from '../animation_effects/vertical_collapse';
import vertical_expand from '../animation_effects/vertical_expand';
import { animation_state } from '../animation_state_types';
import { restore_scope } from './restore_state';
import step_creator_type from './step_creator_type';

const scope_change_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {

    async function animate(speed: number) {
        await vertical_collapse(scope_element, speed);
        restore_scope(after, scope_element);
        await vertical_expand(scope_element, speed);
    }

    async function rollback(speed: number) {
        restore_scope(before, scope_element);
    }

    return {
        name: "scope change",
        animate,
        rollback
    }

}

export default scope_change_step_creator;