import ReactDOMServer from 'react-dom/server';
import ASTComponent from '../../code_editors/js/ast_components/ASTComponent';
import { nearley_custom_object } from '../../code_editors/js/interpreter/ast_types';
import create_ast_expand_struct from '../animation_effects/create_ast_expand_struct';
import { expval_replacements_dict_type } from '../animation_state_types';


export default function get_element_from_ast(ast: nearley_custom_object, replacements_dict: expval_replacements_dict_type) {
    const wrapper = document.createElement("div");
    let element_string = ReactDOMServer.renderToString(
        ASTComponent({ ast_object: ast })
    );
    wrapper.innerHTML = element_string;
    const returned = wrapper.firstChild as Element;

    // TODO: this is very inefficient although efficiency is not a big concern here now;
    for (const key of Object.keys(replacements_dict)) {
        const ast_id = Number(key);
        const ast_elements = wrapper.getElementsByClassName("ast-object-" + ast_id);
        if (ast_elements.length > 0) {
            const ast_element = ast_elements[0];
            ast_element.innerHTML = "";
            const expandable_struct = (replacements_dict[ast_id].expand_struct);
            ast_element.appendChild(expandable_struct.element);
        }
    }
    return returned;
}