import set_replacement from '../animation_effects/set_replacement';
import step_creator_type from './step_creator_type';
import { animation_state, assignment_info_type } from '../animation_state_types';
import create_variable_element from '../animation_effects/create_scope_var_element';
import highlight_element from '../animation_effects/highlight_element';
import highlight_ast_object from '../animation_effects/highlight_ast_object';
import clear_highlights from '../animation_effects/clear_highlights';
import scroll_to from '../animation_effects/scroll_to';


const assignment_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {

    const change = after.last_state_change as assignment_info_type;
    const change_details = change.details;

    async function animate(speed: number) {
        const variable_element = scope_element.getElementsByClassName("scope-var-id-" + change_details.variable.id)[0];
        const new_var_element = create_variable_element(change_details.variable);
        const variable_value_emt = variable_element.getElementsByClassName("scope-value")[0];
        const new_content = new_var_element.getElementsByClassName("scope-value")[0];

        console.log({ variable_value_emt }, { new_content });
        await Promise.all(
            [
                highlight_ast_object(code_element, change_details.ast_origin.id, speed),
                set_replacement(scope_element.getElementsByClassName("scrollable-y")[0], variable_value_emt, new_content, speed)
            ]
        )

        highlight_element(code_element);
    }

    async function rollback(speed: number) {

        const variable_element = scope_element.getElementsByClassName("scope-var-id-" + change_details.variable.id)[0];
        const old_var = before.scope!.get_variable(change_details.variable.name);
        const old_var_element = create_variable_element(old_var!);
        const variable_value_emt = variable_element.getElementsByClassName("scope-value")[0];
        const new_content = old_var_element.getElementsByClassName("scope-value")[0];

        console.log({ variable_value_emt }, { new_content });
        await set_replacement(scope_element.getElementsByClassName("scrollable-y")[0], variable_value_emt, new_content, speed);
        clear_highlights(code_element);
        if (before.highlighted_ast_ids.length > 0) {
            // highlight_element(code_element, "lowlight");
            for (const id of before.highlighted_ast_ids) {
                const el = code_element.getElementsByClassName("ast-object-" + id)[0];
                await scroll_to(el, code_element, speed);
                await highlight_ast_object(code_element, id, speed);
            }
        }

    }

    const assignment_step = {
        name: "variable assignment",
        animate,
        rollback
    }

    return assignment_step;


}

export default assignment_step_creator;