import set_replacement from '../animation_effects/set_replacement';
import { is_binary_expression, is_function_definition, is_constant_expression, get_ast_dependency_tree } from "../../code_editors/js/interpreter/ast_types";
import step_creator_type from './step_creator_type';
import { animation_state, before_function_call_info_type, is_evaluation_change } from '../animation_state_types';
import get_element_from_ast from './get_element_from_ast';
import scroll_to from '../animation_effects/scroll_to';
import create_ast_expand_struct from '../animation_effects/create_ast_expand_struct';
import { mock_token_element } from '../get_representation_levels';


const before_function_call_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {

    let change = after.last_state_change as before_function_call_info_type;


    async function animate(speed: number) {
        // restore function:
        const fun = change.details.function;
        const function_element = code_element.getElementsByClassName("ast-object-" + fun.id)[0];
        const function_element_restored = get_element_from_ast(fun, {});
        await scroll_to(function_element, code_element, speed);
        await set_replacement(code_element, function_element, function_element_restored, speed);

        // display parameters
        const ast_required_parameters_list = fun.parameters_list.list;
        const replacement_promises = [];
        const scroll_promises = [];
        for (let i = 0; i < change.details.parameter_values.length; i++) {
            const value = change.details.parameter_values[i];
            const value_replacement_element = create_ast_expand_struct(value).element;
            const required_parameter = ast_required_parameters_list[i];

            if (required_parameter.optional_assignment_fragment) {
                const value_id = required_parameter.optional_assignment_fragment.value.id;
                const value_emt = code_element.getElementsByClassName("ast-object-" + value_id)[0];
                scroll_promises.push(
                    scroll_to(value_emt, code_element, speed)
                );
                replacement_promises.push(
                    set_replacement(code_element, value_emt, value_replacement_element, speed)
                );
            } else {
                const parameter_element = code_element.getElementsByClassName("ast-object-" + required_parameter.id)[0];
                const dummy = document.createElement("span");
                parameter_element.appendChild(dummy);
                const value_wrapper = document.createElement("span");
                const equals_sign = mock_token_element("=", "binary_op");
                value_wrapper.appendChild(equals_sign);
                value_wrapper.appendChild(value_replacement_element);
                scroll_promises.push(
                    scroll_to(dummy, code_element, speed)
                );
                replacement_promises.push(
                    set_replacement(code_element, dummy, value_wrapper, speed)
                );
            }
        }

        await scroll_promises[0];
        await Promise.all(replacement_promises);
    }

    async function rollback(speed: number) {
    }

    return {
        name: "evaluation replacement",
        animate,
        rollback
    }

}

export default before_function_call_step_creator;