import add_to_scope from "../animation_effects/add_to_scope";
import create_scope from "../animation_effects/create_scope";
import create_variable_element from "../animation_effects/create_scope_var_element";
import { animation_state } from "../animation_state_types";
import get_element_from_ast from "./get_element_from_ast";

export function restore_code(state: animation_state, code_container: Element) {
    const replacements_dict = state.expression_value_replacements;
    const program = state.ast_program;
    const code = code_container.getElementsByClassName("code")[0];
    const code_wrapper = code ? code : code_container;
    if (program) {
        const  code_inner_element = get_element_from_ast(program, replacements_dict);
        code_wrapper.innerHTML = "";
        console.log(replacements_dict)
        // const  code_inner_element = get_element_from_ast(program, replacements_dict);
        code_wrapper.appendChild(code_inner_element);
    }
};

export function restore_scope(state: animation_state, scope_container: Element) {
    const scope_manager = state.scope;
    const main_scope = scope_container.getElementsByClassName("main-scope")[0];
    main_scope.classList.add("current-scope");
    main_scope.innerHTML = "";

    if (!scope_manager) return;
    for (const scope of scope_manager.scopes_list) {
        create_scope(scope_container);
        for (const variable_name of Object.keys(scope.scoped_variables)) {
            const variable = scope.get_variable(variable_name);
            const variable_element = create_variable_element(variable!);
            add_to_scope(scope_container, variable_element);
        }
    }
};

export default function restore_state(state: animation_state, code_container: Element, scope_container: Element) {
    restore_code(state, code_container);
    restore_scope(state, scope_container);
};