import set_replacement from '../animation_effects/set_replacement';
import { is_binary_expression, is_function_definition, is_constant_expression, is_identifier_expression } from "../../code_editors/js/interpreter/ast_types";
import step_creator_type from './step_creator_type';
import { animation_state, is_evaluation_change } from '../animation_state_types';
import get_element_from_ast from './get_element_from_ast';
import scroll_to from '../animation_effects/scroll_to';
import search_identifier from '../animation_effects/search_identifier_effect';
import restore_state from './restore_state';
import highlight_ast_object from '../animation_effects/highlight_ast_object';


const evaluation_replacement_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {

    if (!after.last_state_change) return null;
    if (!is_evaluation_change(after.last_state_change)) return null;
    const last_change_details = after.last_state_change.details;
    // const returned_value = last_change_details.returned_value;
    const ast_object = last_change_details.evaluated_ast;
    const ast_id = ast_object.id;

    async function animate(speed: number) {
        const ast_element = code_element.getElementsByClassName("ast-object-" + ast_id)[0];
        ast_element.innerHTML = ast_element.innerHTML; //this prevents any unwanted poping during get_element_from_ast
        let replacement_element = get_element_from_ast(ast_object, after.expression_value_replacements);
        await scroll_to(ast_element, code_element, speed);
        if (is_identifier_expression(ast_object)) {
            await search_identifier(ast_object.token.value, after.scope!, scope_element, speed);
        }
        // console.log({ ast_element }, { replacement_element });
        // console.log({ code_element })
        await set_replacement(code_element, ast_element, replacement_element, speed);
    }

    async function rollback(speed: number) {
        restore_state(before, code_element, scope_element);
        if (before.highlighted_ast_ids.length > 0) {
            // highlight_element(code_element, "lowlight");
            for (const id of before.highlighted_ast_ids) {
                const el = code_element.getElementsByClassName("ast-object-" + id)[0];
                await scroll_to(el, code_element, speed);
                await highlight_ast_object(code_element, id, speed);
            }
        }
    }

    return {
        name: "evaluation replacement",
        animate,
        rollback
    }

}

export default evaluation_replacement_step_creator;