import React, { useRef, useState, useEffect } from 'react';
import { connect } from 'react-redux';
// import Button from 'react-bootstrap/Button';
import ASTExecuter from '../code_editors/js/interpreter/ast_executer';
import animation_step_type from './animation_step_type';
import AnimationStepsCreator from './animation_steps_creator';
import { program_type } from '../code_editors/js/interpreter/ast_types';

// import './code_animator.css';
import parser_creator from '../code_editors/js/interpreter/n_parser';
import Toolbar from './Toolbar';
import CodeRenderer from '../code_editors/js/code_components/CodeRenderer';
import { playback_state_change_ref, playback_state } from './AnimationControls';
import './CodeEditor.css';
import 'toastr/build/toastr.css';
import './toastr_fix.css';
const toastr = require("toastr");

toastr.options.positionClass = "toast-bottom-left";

type props_type = {
    code: string
}

type ast_program_state_type = program_type | null;


function CodeAminator(props: props_type) {
    const initial_speed = 300;
    let executer_ref = useRef<ASTExecuter | null>(null);
    let code_ref = useRef<string>(props.code);
    let speed_ref = useRef(initial_speed);
    const [astProgramState, setASTProgramState] = useState<ast_program_state_type>(null);
    const on_playback_state_change_ref: playback_state_change_ref = useRef<(s: playback_state) => void>(() => { });
    let animating_flag_ref = useRef(false);
    let step_info_flag_ref = useRef(false);
    let pause_flag_ref = useRef(false);
    let steps_creator_ref = useRef<AnimationStepsCreator | null>(null);
    let animation_steps_ref = useRef<animation_step_type[]>([]);
    let current_step_ref = useRef(0);
    let containers_ref = React.createRef<HTMLDivElement>();

    useEffect(() => {
        animation_steps_ref.current = [];
        current_step_ref.current = 0;
        // speed_ref.current = initial_speed;

        if (astProgramState && containers_ref.current) {
            const code_container = containers_ref.current.getElementsByClassName("code-viewer")[0];
            const scope_container = containers_ref.current.getElementsByClassName("scopes-container")[0];
            // const scope_container = containers_ref.current.querySelector(".scopes-container .scrollable-y");
            steps_creator_ref.current = new AnimationStepsCreator(code_container, scope_container!);
            let executer_options = steps_creator_ref.current.get_callbacks_dictionary();
            // toastr.info("new executer");
            executer_ref.current = new ASTExecuter(executer_options);
            executer_ref.current.init(astProgramState);
        } else {

            executer_ref.current = null;
        }
    },
        // []
    );

    function on_code_change(code: string) {
        // console.log("code_changed_to", code)
        code_ref.current = code;
    };


    function start_animation_flag() {
        animating_flag_ref.current = true;
    };

    function stop_animation_flag() {
        animating_flag_ref.current = false;
    };


    async function animate_next() {
        if (executer_ref.current && steps_creator_ref.current && !executer_ref.current.execution_finished) {
            executer_ref.current.execute();
            animation_steps_ref.current = steps_creator_ref.current.get_animation_steps();
        }
        if (current_step_ref.current < animation_steps_ref.current.length && !animating_flag_ref.current) {
            start_animation_flag();
            const animation_step = animation_steps_ref.current[current_step_ref.current];
            if (step_info_flag_ref.current)
                toastr.info(animation_step.name);
            await animation_step.animate(speed_ref.current);
            current_step_ref.current++;
            stop_animation_flag();
            return true;
        }
        return false;
    }

    async function animate_previous() {
        if (current_step_ref.current > 0) {
            start_animation_flag();
            const animation_step = animation_steps_ref.current[current_step_ref.current - 1];
            if (step_info_flag_ref.current)
                toastr.info(animation_step.name+" rolled back");
            await animation_step.rollback(speed_ref.current);
            current_step_ref.current--;
            stop_animation_flag();
            return true;
        }
        return false;
    }


    async function step_forward_handler() {
        on_playback_state_change_ref.current("playing_step_forward");
        step_info_flag_ref.current = true;
        await animate_next();
        step_info_flag_ref.current = false;
        on_playback_state_change_ref.current("paused");
    }

    async function step_backward_handler() {
        on_playback_state_change_ref.current("playing_step_forward");
        step_info_flag_ref.current = true;
        await animate_previous();
        step_info_flag_ref.current = false;
        on_playback_state_change_ref.current("paused");
    }

    async function play_handler() {
        on_playback_state_change_ref.current("playing");
        pause_flag_ref.current = false;
        let animate_result = true;
        while (animate_result && !pause_flag_ref.current) {
            animate_result = await animate_next();
        }
        console.log("animation finished");
        on_playback_state_change_ref.current("paused");
    }

    async function rollback_all_handler() {
        recalculate_program();
        // on_playback_state_change_ref.current("playing_backwards");
        // pause_flag_ref.current = false;
        // let animate_result = true;
        // while (animate_result && !pause_flag_ref.current) {
        //     animate_result = await animate_previous();
        // }
        // console.log("animation rolled_back");
        on_playback_state_change_ref.current("paused");
    }

    function speed_change_handler(speed: number) {
        speed_ref.current = speed;
    }

    function pause_handler() {
        pause_flag_ref.current = true;
    }

    function recalculate_program() {
        const parser = parser_creator();
        try {
            parser.feed(code_ref.current);
            console.log(code_ref.current);
            const results_array = parser.results as any[];
            if (results_array.length > 1) {
                alert("multiple interpretations");
            }
            const new_program = results_array[0];
            console.log(new_program);
            if (new_program) {
                setASTProgramState(new_program);
                console.log("holy shit", new_program);
            }else{
                toastr.error("syntax error -U");
            }
        } catch (error) {
            console.error(error);
            toastr.error("syntax error");
        }
    }

    function toggle_mode_handler() {
        if (!astProgramState) {
            recalculate_program();
        } else {
            setASTProgramState(null);
        }
    }

    // function toggle_scope_visibility() {
    //     if (containers_ref.current) {
    //         const scope_container = containers_ref.current.getElementsByClassName("scopes-container")[0];
    //         if (scope_container?.classList.contains("show")) {
    //             scope_container.classList.remove("hidden");
    //             scope_container.classList.remove("show");
    //         } else if (scope_container) {
    //             scope_container.classList.remove("hidden");
    //             scope_container.classList.add("show");
    //         } 
    //     }
    // }



    return (
        <div className="code-animator">
            <div className="containers" ref={containers_ref}>

                <CodeRenderer
                    on_code_change={on_code_change}
                    code={code_ref.current}
                    program_status={astProgramState}
                />

                <Toolbar
                    state_mode={astProgramState ? "playback" : "edit"}
                    on_playback_state_change_ref={on_playback_state_change_ref}
                    toggle_mode_handler={toggle_mode_handler}
                    rollback_all_handler={rollback_all_handler}
                    play_handler={play_handler}
                    pause_handler={pause_handler}
                    step_forward_handler={step_forward_handler}
                    step_backward_handler={step_backward_handler}
                    speed_change_handler={speed_change_handler}
                    default_speed={speed_ref.current}
                />
            </div>
        </div>
    );
};

const map_state_to_props = (state: any) => (
    {
        code: state.globals_reducer.code,
    }
);

export default connect(map_state_to_props, null)(CodeAminator);
// export default CodeAminator;
