import add_to_scope from '../animation_effects/add_to_scope';
import create_variable_element from '../animation_effects/create_scope_var_element';
import drag_element_to_another from '../animation_effects/drag_one_element_to_another';
import fade_in from '../animation_effects/fade_in';
import fade_out from '../animation_effects/fade_out';
import hide_scope from '../animation_effects/hide_scope';
import remove_from_scope from '../animation_effects/remove_from_scope';
import scroll_to from '../animation_effects/scroll_to';
import show_scope from '../animation_effects/show_scope';
import { add_to_scope_info_type, animation_state } from '../animation_state_types';
import animation_step_type from '../animation_step_type';
import step_creator_type from './step_creator_type';

const starting_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {


    const step: animation_step_type = {
        name: "show scope",
        animate: async (speed: number) => {
            const current_scope = <HTMLElement>scope_element.getElementsByClassName("current-scope")[0];
            current_scope.style.opacity = "0";
            await show_scope(scope_element, speed);
            await fade_in(current_scope, speed);
            
            
            
        },
        rollback: async (speed: number) => {
            const current_scope = <HTMLElement>scope_element.getElementsByClassName("current-scope")[0];
            await hide_scope(scope_element, speed);
            await fade_out(current_scope, speed);
        }
    }

    return step;
    // const show_scope_step = create_show_scope_step(scope_container);
    // const hide_scope_step = create_hide_scope_step(scope_container);
    // return merge_steps([show_scope_step, step, hide_scope_step]);

}

export default starting_step_creator;
