import ScopeVariable from "../../code_editors/js/interpreter/parsing_files/variable";

import create_expandable_value_element from './create_ast_expand_struct';
export default function create_variable_element(variable: ScopeVariable) {
    const variable_name = variable.name;
    let scope_element = document.createElement("div");
    // scope_element.style.position = "relative";
    scope_element.classList.add("scope-var");
    scope_element.classList.add("scope-var-" + variable_name);
    scope_element.classList.add("scope-var-id-" + variable.id);
    const scope_identifier = document.createElement("span");
    scope_identifier.classList.add("scope-identifier");
    scope_identifier.innerHTML = variable_name;
    const scope_equal_sign = document.createElement("span");
    scope_equal_sign.classList.add("scope-equal-sign");
    scope_equal_sign.innerHTML = " = ";
    const scope_value = create_expandable_value_element(variable.value).element;
    scope_element.appendChild(scope_identifier);
    scope_element.appendChild(scope_equal_sign);
    scope_element.appendChild(scope_value);



    return scope_element;
}