import React, { useContext } from 'react';
import ReactDOMServer from 'react-dom/server';
import ASTComponent from '../ast_components/ASTComponent';
import { program_type } from '../interpreter/ast_types';

type props_type = {
    ast_program: program_type
}

export default function ASTBasedCode(props: props_type) {
    let ast_program = props.ast_program;
    console.log(ast_program);
    return (
        <div className="height-100 pos-rel"
            dangerouslySetInnerHTML={{
                __html: ReactDOMServer.renderToString(
                    <>
                        <div
                            // ref={code_mirror}
                            className="code-viewer nice-scroll-bar"
                        >
                            <div className="code-line-references">
                                {/* {line_references} */}
                            </div>
                            <pre className="code" spellCheck="false">
                                <ASTComponent ast_object={ast_program} />
                            </pre>

                        </div >
                        <div className="scopes-wrapper hidden">
                            <div className="scopes-container">
                                <div className="height-100 nice-scroll-bar scrollable-y pos-rel">

                                    <div className="scopes-fullheight-wrapper">

                                        <div className="scope main-scope current-scope">

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </>
                )
            }}
        >

        </div>
    )
}
