import gsap from 'gsap';
export default async function vertical_expand(el: Element, speed: number) {
    const htmlel = el as HTMLElement;
    const dummy = el.cloneNode(true) as HTMLElement;
    htmlel.after(dummy);
    const final_height = htmlel.getBoundingClientRect().height;
    htmlel.remove();
    dummy.style.height = "0px";
    dummy.style.overflow= "hidden";
    await gsap.to(dummy, { duration: speed / 1000, height: final_height });
    dummy.after(htmlel);
    dummy.remove();
}