import { counter_ref, expression_value_type, function_type, is_array_value, is_function_value, function_value, is_object_value, is_internal_method, internal_method_value, general_internal_method_value, expression_value, string_value, number_value, boolean_value } from './parsing_files/expression_value_types'
import { default_undefined_value, get_default_array_value, get_default_object_value } from './parsing_files/default_expression_values';

import ScopeManager from './parsing_files/scope_objects';
import { statement_type, expression_type, for_statement_type, function_definition_type, program_type, expression_statement_type, block_type, if_statement_type, declaration_statement_type, declaration_fragment_type, single_declaration_type, group_expression_type, binary_operation_type, is_declaration_fragment, is_expression, is_binary_expression, is_group_expression, is_function_definition, is_constant_expression, is_identifier_expression, identifier_expression_type, constant_expression_type, expression_call_type, is_expression_call, array_expression_type, is_array_expression, is_object_expression, object_expression_type, property_expression_type, is_property_expression, access_by_index_expression_type, is_access_by_index_expression, nearley_custom_object, is_return_statement, is_break_statement, is_continue_statement, break_statement_type, continue_statement_type } from './ast_types';
import { is_block, is_declaration_statement, is_if_statement, is_for_statement, is_expression_statement } from './ast_types';
import clone_unlinked_expression_value from './parsing_files/expression_value_converter';
import ScopeVariable from './parsing_files/variable';

export type executer_options = {
    on_program_start?: (ast_program: program_type, scope: ScopeManager) => void,
    on_before_execution?: (ast_statement: statement_type, scope: ScopeManager) => void,
    on_after_execution?: (ast_statement: statement_type, scope: ScopeManager) => void,
    on_before_evaluation?: (ast_expression: expression_type, scope: ScopeManager) => void,
    on_after_evaluation?: (ast_expression: expression_type, scope: ScopeManager, returned_value: expression_value) => void,
    on_before_for_loop?: (ast_expression: for_statement_type) => void,
    on_after_for_loop?: (ast_expression: for_statement_type) => void,
    on_index_evaluation?: (ast_expression: access_by_index_expression_type, index_expression_value: string) => void,
    on_after_if_condition_eval?: (ast_expression: if_statement_type, evaluation_result: expression_value) => void,
    on_variable_declaration?: (ast_origin: nearley_custom_object, variable: ScopeVariable, scope_manager: ScopeManager) => void,
    on_assignment?: (ast_origin: nearley_custom_object, variable: ScopeVariable, scope_manager: ScopeManager) => void,
    on_value_update?: (e_val: expression_value) => void,
    on_before_function_call?: (function_definition: function_definition_type, function_call_expression: expression_call_type, evaluated_parameters: expression_value[]) => void,
    on_function_call?: (function_definition: function_definition_type) => void,
    on_scope_change?: (scope_manager: ScopeManager) => void,
    on_new_scope?: (scope_manager: ScopeManager) => void,
    on_scope_end?: (scope_manager: ScopeManager) => void,
};

type execution_return_type = 'complete' | 'failed';

type evaluation_return_type = {
    execution_result: execution_return_type,
    value: expression_value
}


const get_default_failed_evaluation = (next_value_id: number): evaluation_return_type => {
    const default_failed_evaluation: evaluation_return_type = {
        execution_result: "failed",
        value: default_undefined_value
    }
    return default_failed_evaluation;
}

const get_default_undefined_evaluation = (next_value_id: number): evaluation_return_type => {
    const default_undefined_evaluation: evaluation_return_type = {
        execution_result: "complete",
        value: default_undefined_value
    }
    return default_undefined_evaluation;
}

class ASTExecuter {

    options: executer_options;
    value_counter_ref: counter_ref

    closures: { [key: string]: ScopeManager };
    ast_program: program_type | null;
    execution_finished: boolean;
    returned_expression_value: expression_value | null;
    break_flag: boolean;
    continue_flag: boolean;

    constructor(options: executer_options) {

        /* ----------------------------- initial values ----------------------------- */

        // this.processed_ast_objects = new Set();
        // this.partial_evaluations_cache = {};
        this.ast_program = null;
        this.execution_finished = false;
        this.closures = {};
        this.returned_expression_value = null;
        this.break_flag = false;
        this.continue_flag = false;
        this.options = options;
        this.value_counter_ref = {
            current: 0,
        };
    }

    init(ast_program: program_type) {
        this.ast_program = ast_program;
    }

    /* -------------------------------------------------------------------------- */
    /*                            statement executions                            */
    /* -------------------------------------------------------------------------- */

    execute(scope_manager: ScopeManager | null = null) {
        const sc_man = scope_manager ? scope_manager : new ScopeManager();

        if (this.ast_program)
            this.execute_program(this.ast_program, sc_man);
    }

    execute_statement(ast_statement: statement_type, scope_manager: ScopeManager): execution_return_type {
        if (this.returned_expression_value)
            return "complete";
        if (this.break_flag)
            return "complete";
        if (this.continue_flag)
            return "complete";

        let executed: execution_return_type = "failed";
        if (this.options.on_before_execution)
            this.options.on_before_execution(ast_statement, scope_manager.clone_unlinked());

        if (is_block(ast_statement))
            executed = this.execute_block(ast_statement, scope_manager);
        else if (is_declaration_statement(ast_statement))
            executed = this.execute_declaration(ast_statement, scope_manager);
        else if (is_if_statement(ast_statement))
            executed = this.execute_if_statement(ast_statement, scope_manager);
        else if (is_for_statement(ast_statement))
            executed = this.execute_for_statement(ast_statement, scope_manager);
        else if (is_expression_statement(ast_statement))
            executed = this.execute_expression_statement(ast_statement, scope_manager);
        else if (is_return_statement(ast_statement))
            executed = this.execute_return_statement(ast_statement, scope_manager);
        else if (is_break_statement(ast_statement))
            executed = this.execute_break_statement(ast_statement, scope_manager);
        else if (is_continue_statement(ast_statement))
            executed = this.execute_continue_statement(ast_statement, scope_manager);

        else
            executed = this.statement_type_not_found(ast_statement);


        if (executed === "failed") {
            console.error("execution failed for ", ast_statement);
            this.execution_finished = true;
        }

        if (executed === "complete") {
            if (this.options.on_after_execution)
                this.options.on_after_execution(ast_statement, scope_manager.clone_unlinked());
        }

        return executed;
    }

    statement_type_not_found(ast_statement: statement_type): execution_return_type {
        console.error("execute method not found for statement: ", ast_statement);
        return "failed";
    }

    execute_return_statement(ast_statement: expression_statement_type, scope_manager: ScopeManager): execution_return_type {
        let expression_result = this.evaluate(ast_statement.expression, scope_manager);
        if (expression_result.execution_result === "failed")
            return "failed";
        this.returned_expression_value = expression_result.value;
        return "complete";
    }
    execute_break_statement(ast_statement: break_statement_type, scope_manager: ScopeManager): execution_return_type {
        this.break_flag = true;
        return "complete";
    }
    execute_continue_statement(ast_statement: continue_statement_type, scope_manager: ScopeManager): execution_return_type {
        this.continue_flag = true;
        return "complete";
    }

    execute_expression_statement(ast_statement: expression_statement_type, scope_manager: ScopeManager): execution_return_type {
        let expression_result = this.evaluate(ast_statement.expression, scope_manager);
        if (expression_result.execution_result === "failed")
            return "failed";
        return "complete";
    }

    start_scope(scope_manager: ScopeManager) {
        scope_manager.start_scope();
        if (this.options.on_new_scope) {
            this.options.on_new_scope(scope_manager.clone_unlinked());
        }
    }

    end_scope(scope_manager: ScopeManager) {
        scope_manager.end_scope();
        if (this.options.on_scope_end) {
            this.options.on_scope_end(scope_manager.clone_unlinked());
        }
    }

    execute_block(block_object: block_type, scope_manager: ScopeManager): execution_return_type {
        this.start_scope(scope_manager);
        let block_statements = block_object.statements_list_node.list;
        for (let i = 0; i < block_statements.length; i++) {
            const statement = block_statements[i];
            const child_execution_result = this.execute_statement(statement, scope_manager);
            if (child_execution_result === "failed") {
                return "failed";
            }
        }

        // for (const statement of block_statements) {
        //     this.processed_ast_objects.delete(statement.id);
        // }
        this.end_scope(scope_manager);
        return "complete";
    }

    execute_program(program_object: program_type, scope_manager: ScopeManager): execution_return_type {
        scope_manager.start_scope();
        if (this.options.on_program_start)
            this.options.on_program_start(program_object, scope_manager.clone_unlinked());

        let statements = program_object.statements_list_node.list;
        for (let i = 0; i < statements.length; i++) {
            const statement = statements[i];
            const child_execution_result = this.execute_statement(statement, scope_manager);
            if (child_execution_result === "failed") {
                return "failed";
            }
        }
        this.execution_finished = true;
        return "complete";
    }

    execute_if_statement(if_statement: if_statement_type, scope_manager: ScopeManager): execution_return_type {
        const condition = if_statement.condition;
        const evaluation_result = this.evaluate(condition, scope_manager);
        if (this.options.on_after_if_condition_eval) {
            this.options.on_after_if_condition_eval(if_statement, evaluation_result.value);
        }


        if (evaluation_result.execution_result === "failed")
            return "failed";
        const evaluation_result_value = evaluation_result.value;
        if (evaluation_result_value?.value) {
            return this.execute_statement(if_statement.statement, scope_manager);
        } else {
            if (if_statement.else_fragment) {
                return this.execute_statement(if_statement.else_fragment.statement, scope_manager);
            }
        }
        return "complete";
    }

    execute_for_statement(for_object: for_statement_type, scope_manager: ScopeManager): execution_return_type {
        this.start_scope(scope_manager);
        const initial_fragment = for_object.for_init;
        const condition = for_object.condition;
        const final_expression = for_object.final_expression;
        const statement = for_object.statement;
        const me = this;
        let execution_return: execution_return_type = "complete";

        function evaluate_condition(): boolean {
            let condition_result = me.evaluate(condition, scope_manager);
            if (condition_result.execution_result === "failed")
                execution_return = "failed";
            else if (condition_result.value) {
                if (condition_result.value.value)
                    return true;
                else
                    return false;
            }

            return false;
        }

        if (initial_fragment) {
            if (is_declaration_fragment(initial_fragment)) {
                this.declare_variables(scope_manager, initial_fragment);
            } else if (is_expression(initial_fragment)) {
                this.evaluate(initial_fragment, scope_manager);
            }
        }

        let counter = 0;

        while ((!this.returned_expression_value && !this.break_flag)) {
            this.continue_flag = false;
            if (this.options.on_before_for_loop)
                this.options.on_before_for_loop(for_object);
            if (!evaluate_condition()) break;
            let execution_result = this.execute_statement(statement, scope_manager);
            if (execution_result === "failed") return "failed";
            if ((!this.returned_expression_value && !this.break_flag)) {
                let evaluation_result = this.evaluate(final_expression, scope_manager);
                if (this.options.on_after_for_loop)
                    this.options.on_after_for_loop(for_object);
                if (evaluation_result.execution_result == "failed") {
                    return "failed";
                }
                counter++;
                if (counter > 1000) {
                    console.log({ ...scope_manager });
                    console.error("max iteration depth exeded");
                    return "failed";
                }
            }
        }

        this.break_flag = false;
        this.end_scope(scope_manager);
        return execution_return;
    }


    add_variable_to_scope(ast_origin: nearley_custom_object, scope_manager: ScopeManager, variable_name: string, value: expression_value = get_default_undefined_evaluation(++this.value_counter_ref.current).value) {
        scope_manager.add_variable(variable_name, value);
        // if (this.options.on_variable_declaration && !scope_manager.is_closure)
        const created_variable = scope_manager.get_variable(variable_name);
        if (this.options.on_variable_declaration && created_variable)
            this.options.on_variable_declaration(ast_origin, created_variable.clone_unlinked(), scope_manager.clone_unlinked());
    }

    declare_variable(scope_manager: ScopeManager, declaration: single_declaration_type) {
        let variable_name = declaration.identifier.value;
        let value = get_default_undefined_evaluation(++this.value_counter_ref.current).value;
        if (declaration.optional_assignment_fragment) {
            let eval_result = this.evaluate(declaration.optional_assignment_fragment.value, scope_manager);
            value = eval_result.value;
        }
        this.add_variable_to_scope(declaration, scope_manager, variable_name, value);
    }

    declare_variables(scope_manager: ScopeManager, declaration_fragment: declaration_fragment_type) {
        let declarations = declaration_fragment.declarations_list.list;
        for (const declaration of declarations) {
            this.declare_variable(scope_manager, declaration)
        }
    }

    execute_declaration(declaration_object: declaration_statement_type, scope_manager: ScopeManager): execution_return_type {
        try {
            this.declare_variables(scope_manager, declaration_object.declaration_fragment);
            return "complete";
        } catch (error) {
            console.error(error);
            return "failed";
        }
    }


    /* -------------------------------------------------------------------------- */
    /*                           expression evaluations                           */
    /* -------------------------------------------------------------------------- */

    evaluate(expression: expression_type, scope_manager: ScopeManager): evaluation_return_type {


        let execution_result: execution_return_type;
        if (this.options.on_before_evaluation)
            this.options.on_before_evaluation(expression, scope_manager.clone_unlinked())
        let returned: evaluation_return_type = get_default_failed_evaluation(++this.value_counter_ref.current);

        if (is_constant_expression(expression))
            returned = this.evaluate_constant(expression, scope_manager);
        else if (is_identifier_expression(expression))
            returned = this.evaluate_identifier(expression, scope_manager);
        else if (is_binary_expression(expression))
            returned = this.evaluate_binary(expression, scope_manager);
        else if (is_group_expression(expression))
            returned = this.evaluate_group(expression, scope_manager);
        else if (is_function_definition(expression))
            returned = this.evaluate_function_definition(expression, scope_manager);
        else if (is_expression_call(expression))
            returned = this.evaluate_expression_call(expression, scope_manager);
        else if (is_array_expression(expression))
            returned = this.evaluate_array(expression, scope_manager);
        else if (is_object_expression(expression))
            returned = this.evaluate_object(expression, scope_manager);
        else if (is_property_expression(expression))
            returned = this.evaluate_property(expression, scope_manager);
        else if (is_access_by_index_expression(expression))
            returned = this.evaluate_access_by_index_expression(expression, scope_manager);
        else
            this.expression_type_not_found(expression);

        execution_result = returned.execution_result;

        if (execution_result === "failed") {
            console.error("evaluation failed for ", expression);
        }

        if (execution_result === "complete") {
            if (this.options.on_after_evaluation)
                this.options.on_after_evaluation(expression, scope_manager.clone_unlinked(), returned.value);
        }
        return returned;
    }

    expression_type_not_found(ast_expression: expression_type): evaluation_return_type {
        console.error("evaluate method not found for expression: ", ast_expression);
        return get_default_failed_evaluation(++this.value_counter_ref.current);
    }

    evaluate_function_definition(function_expression: function_definition_type, scope_manager: ScopeManager): evaluation_return_type {
        let scope_manager_clone = scope_manager.clone_as_closure();
        scope_manager_clone.is_closure = true;

        let function_structure: function_type = {
            function: function_expression,
            scope: scope_manager_clone
        }

        let expression_function_value: expression_value = {
            id: ++this.value_counter_ref.current,
            value: function_structure,
            type: "function",
        };

        return {
            value: expression_function_value,
            execution_result: "complete"
        }
    }

    evaluate_array(array_expression: array_expression_type, scope_manager: ScopeManager): evaluation_return_type {
        const expressions_list = array_expression.items_list.list;
        const returned_value: expression_value = get_default_array_value(this.value_counter_ref);
        for (let i = 0; i < expressions_list.length; i++) {
            const expression = expressions_list[i];
            const expression_eval_result = this.evaluate(expression, scope_manager);
            if (expression_eval_result.execution_result == "failed") return get_default_failed_evaluation(++this.value_counter_ref.current);;
            returned_value.value.push(expression_eval_result.value);
        }

        return {
            execution_result: "complete",
            value: returned_value,
        }
    }

    evaluate_object(object_expression: object_expression_type, scope_manager: ScopeManager): evaluation_return_type {
        const key_value_pair_list = object_expression.key_value_pairs_list.list;
        const returned_value: expression_value = get_default_object_value(this.value_counter_ref);
        const keys: string[] = [];
        for (let i = 0; i < key_value_pair_list.length; i++) {
            const key_value_pair = key_value_pair_list[i];
            const key = key_value_pair.key.value;
            if (!keys.includes(key))
                keys.push(key);
            else {
                console.error("same key twice for expression:", object_expression);
                return get_default_failed_evaluation(++this.value_counter_ref.current);;
            }
            const expression_eval_result = this.evaluate(key_value_pair.value, scope_manager);
            if (expression_eval_result.execution_result == "failed") return get_default_failed_evaluation(++this.value_counter_ref.current);;
            returned_value.value[key] = expression_eval_result.value;
        }

        return {
            execution_result: "complete",
            value: returned_value,
        }
    }

    evaluate_function_call(expression_val: function_value, function_call_expression: expression_call_type, scope_manager: ScopeManager): evaluation_return_type {

        let function_with_scope = expression_val.value;
        let function_scope = function_with_scope.scope;
        let call_parameter_expressions = function_call_expression.call_parameters.list;
        let required_parameters = function_with_scope.function.parameters_list.list;
        if (call_parameter_expressions.length !== required_parameters.length) {
            console.error("incorrect number of parameters for", function_call_expression);
            return get_default_failed_evaluation(++this.value_counter_ref.current);;
        }

        this.start_scope(function_scope);
        const evaluated_parameters = [];
        for (let i = 0; i < required_parameters.length; i++) {

            const expression = call_parameter_expressions[i];
            const expression_eval = this.evaluate(expression, scope_manager);
            evaluated_parameters.push(expression_eval.value);
        }

        if (this.options.on_before_function_call) {
            this.options.on_before_function_call(
                expression_val.value.function,
                function_call_expression,
                evaluated_parameters
            );
        }

        for (let i = 0; i < required_parameters.length; i++) {
            const parameter = required_parameters[i];
            const value = evaluated_parameters[i];
            this.add_variable_to_scope(parameter, function_scope, parameter.parameter_identifier.value, value);
        }


        if (this.options.on_scope_change)
            this.options.on_scope_change(function_scope.clone_unlinked());

        const statement_result = this.execute_statement(function_with_scope.function.block_statement, function_scope);

        if (this.options.on_scope_change)
            this.options.on_scope_change(scope_manager.clone_unlinked());


        let returned_value = get_default_undefined_evaluation(++this.value_counter_ref.current).value;

        if (this.returned_expression_value) {
            returned_value = this.returned_expression_value;
            this.returned_expression_value = null;
        }

        if (statement_result !== "failed") {
            if (this.options.on_function_call) {
                this.options.on_function_call(expression_val.value.function);
            }
        }

        return {
            execution_result: statement_result,
            value: returned_value
        }
    }

    evaluate_internal_method_call(expression_val: general_internal_method_value, function_call_expression: expression_call_type, scope_manager: ScopeManager): evaluation_return_type {

        // return get_default_failed_evaluation(++ this.value_counter);;
        let method_info = expression_val.value;
        const caller = method_info.caller;
        let call_parameter_expressions = function_call_expression.call_parameters.list;
        const expression_values: expression_value[] = [];
        for (let i = 0; i < call_parameter_expressions.length; i++) {
            const expression = call_parameter_expressions[i];
            const expression_eval = this.evaluate(expression, scope_manager);
            if (expression_eval.execution_result === "failed") return get_default_failed_evaluation(++this.value_counter_ref.current);;
            expression_values.push(expression_eval.value);
        }

        const method_function = method_info.function;
        const returned_ev = method_function(caller, ...expression_values);
        if (this.options.on_value_update && method_info.method_mutates_caller) {
            // TODO: this has to be handled using immutables, not clone functions
            this.options.on_value_update(clone_unlinked_expression_value(caller))
        }
        return {
            execution_result: "complete",
            value: returned_ev
        }
    }

    evaluate_expression_call(expression_call_expression: expression_call_type, scope_manager: ScopeManager): evaluation_return_type {
        let expression_result = this.evaluate(expression_call_expression.expression, scope_manager);
        if (expression_result.execution_result === "failed") return get_default_failed_evaluation(++this.value_counter_ref.current);;
        const exp_val = expression_result.value;
        if (is_function_value(exp_val)) return this.evaluate_function_call(exp_val, expression_call_expression, scope_manager);
        else if (is_internal_method(exp_val)) return this.evaluate_internal_method_call(exp_val, expression_call_expression, scope_manager);
        else return get_default_failed_evaluation(++this.value_counter_ref.current);;
    }

    evaluate_assignment(left: expression_type, right: expression_type, scope_manager: ScopeManager): evaluation_return_type {
        let expression = right;
        let expression_response = this.evaluate(expression, scope_manager);
        let expression_response_value = expression_response.value;

        if (expression_response.execution_result === "complete") {
            if (is_identifier_expression(left)) {
                const scoped_var = scope_manager.get_variable(left.token.value);

                if (scoped_var) {
                    scoped_var.value = expression_response_value;
                }
                if (this.options.on_assignment && scoped_var) {
                    this.options.on_assignment(right, scoped_var.clone_unlinked(), scope_manager.clone_unlinked())
                }
                return expression_response;

            }
        }
        return get_default_failed_evaluation(++this.value_counter_ref.current);;
    }

    evaluate_group(ast_expression: group_expression_type, scope_manager: ScopeManager): evaluation_return_type {
        return this.evaluate(ast_expression.expression, scope_manager);
    }

    access_object_property(expression_value: expression_value, key: string): evaluation_return_type {
        let returned = get_default_undefined_evaluation(++this.value_counter_ref.current);

        if (is_object_value(expression_value)) {
            if (expression_value.value.hasOwnProperty(key)) {
                returned = {
                    execution_result: "complete",
                    value: expression_value.value[key]
                };
            }
        } else if (is_array_value(expression_value)) {
            if (expression_value.value.hasOwnProperty(key)) {
                const num_key = Number(key);
                if (!isNaN(num_key)) {
                    returned = {
                        execution_result: "complete",
                        value: expression_value.value[num_key]
                    };
                }
            }
        }

        if (expression_value.members) {
            const array_internal_members = expression_value.members;
            if (array_internal_members.hasOwnProperty(key)) {
                return {
                    execution_result: "complete",
                    value: array_internal_members[key](this.value_counter_ref)
                };
            }
        }

        // console.log({ expression_value, returned_property: returned, key })
        return returned;
    }

    evaluate_property(ast_expression: property_expression_type, scope_manager: ScopeManager): evaluation_return_type {
        const object_expression = ast_expression.expression;
        const expression_evaluation = this.evaluate(object_expression, scope_manager);
        if (expression_evaluation.execution_result === "failed") return get_default_failed_evaluation(++this.value_counter_ref.current);;
        const expression_value = expression_evaluation.value;
        const key = ast_expression.property_identifier.value;
        const result = this.access_object_property(expression_value, key);
        return result;
    }

    evaluate_access_by_index_expression(ast_expression: access_by_index_expression_type, scope_manager: ScopeManager): evaluation_return_type {
        const expression_evaluation = this.evaluate(ast_expression.expression, scope_manager);
        if (expression_evaluation.execution_result === "failed") return get_default_failed_evaluation(++this.value_counter_ref.current);;
        const expression_value = expression_evaluation.value;
        const key_expression = ast_expression.index;
        const key_evaluation = this.evaluate(key_expression, scope_manager);
        if (key_evaluation.execution_result === "failed") return get_default_failed_evaluation(++this.value_counter_ref.current);;
        const key_value = key_evaluation.value;
        if (!(key_value.type === "number" || key_value.type === "string")) return get_default_failed_evaluation(++this.value_counter_ref.current);;
        const key_string: string = key_value.type === "string" ? key_value.value : key_value.value;
        if (this.options.on_index_evaluation)
            this.options.on_index_evaluation(ast_expression, key_string);
        return this.access_object_property(expression_value, key_string);
    }


    evaluate_constant(ast_expression: constant_expression_type, scope_manager: ScopeManager): evaluation_return_type {
        let expression_token = ast_expression.token;
        let token_value = expression_token.value;
        let value: any = token_value;
        let type: expression_value_type = "string";
        let returned_expression_value: expression_value | null = null;

        function is_number(value: string) {
            if (value === "") return false;
            return !isNaN(Number(value));
        }

        if (expression_token.type === "string") {
            value = token_value.slice(1, -1);
            let string_ev: string_value = {
                id: ++this.value_counter_ref.current,
                type,
                value,
            }
            returned_expression_value = string_ev;
        }
        else if (expression_token.type === "number") {
            if (is_number(value)) {
                type = "number";
                value = parseFloat(token_value);
                let number_ev: number_value = {
                    id: ++this.value_counter_ref.current,
                    type,
                    value,
                }
                returned_expression_value = number_ev;
            }
        }
        else if (expression_token.type === "true" || expression_token.type === "false") {
            type = "boolean";
            if (expression_token.value === "true")
                value = true;
            else
                value = false;
            let number_ev: boolean_value = {
                id: ++this.value_counter_ref.current,
                type,
                value,
            }
            returned_expression_value = number_ev;
        }

        if (!returned_expression_value)
            return get_default_failed_evaluation(++this.value_counter_ref.current);

        return {
            execution_result: "complete",
            value: returned_expression_value
        }

    }

    evaluate_identifier(ast_expression: identifier_expression_type, scope_manager: ScopeManager): evaluation_return_type {
        const scoped_var = scope_manager.get_variable(ast_expression.token.value);

        if (scoped_var) {
            return {
                execution_result: "complete",
                value: scoped_var.value
            }
        }
        return get_default_failed_evaluation(++this.value_counter_ref.current);;
    }


    // TODO: make a better implementation of this
    evaluate_binary(ast_expression: binary_operation_type, scope_manager: ScopeManager): evaluation_return_type {
        let operator = ast_expression.operator.value;
        if (operator === "=")
            return this.evaluate_assignment(ast_expression.left, ast_expression.right, scope_manager);

        let newval: any = null;
        let left_expression_result = this.evaluate(ast_expression.left, scope_manager);
        if (left_expression_result.execution_result == "failed")
            return get_default_failed_evaluation(++this.value_counter_ref.current);;

        let right_expression_result = this.evaluate(ast_expression.right, scope_manager);
        if (right_expression_result.execution_result == "failed")
            return get_default_failed_evaluation(++this.value_counter_ref.current);;

        const left_expression_value = left_expression_result.value;
        const right_expression_value = right_expression_result.value;



        if (!left_expression_value || !right_expression_value)
            return get_default_failed_evaluation(++this.value_counter_ref.current);

        let type: expression_value_type = left_expression_value.type;

        try {
            switch (operator) {
                case '+':
                    {
                        if (left_expression_value.type === "number" && right_expression_value.type === "number") {
                            const left = parseFloat(left_expression_value?.value);
                            const right = parseFloat(right_expression_value?.value);
                            newval = left + right;
                        } else if (left_expression_value.type === "string" || right_expression_value.type === "string") {
                            const left = left_expression_value?.value;
                            const right = right_expression_value?.value;
                            newval = left + right;
                            type = "string";
                        }
                        break;
                    }
                case '-':
                    {
                        const left = parseFloat(left_expression_value?.value);
                        const right = parseFloat(right_expression_value?.value);
                        newval = left - right;
                        newval = parseFloat(newval);
                        break;
                    }
                case '*':
                    {
                        const left = parseFloat(left_expression_value?.value);
                        const right = parseFloat(right_expression_value?.value);
                        newval = left * right;
                        newval = parseFloat(newval);
                        break;
                    }
                case '/':
                    {
                        const left = parseFloat(left_expression_value?.value);
                        const right = parseFloat(right_expression_value?.value);
                        newval = left / right;
                        newval = parseFloat(newval);
                        break;
                    }
                case '<':
                    {
                        const left = parseFloat(left_expression_value?.value);
                        const right = parseFloat(right_expression_value?.value);
                        newval = (left < right);
                        type = "boolean";
                        break;
                    }
                case '>':
                    {
                        const left = parseFloat(left_expression_value?.value);
                        const right = parseFloat(right_expression_value?.value);
                        newval = (left > right);
                        type = "boolean";
                        break;
                    }
                case '==':
                    {
                        newval = (left_expression_value?.value == right_expression_value?.value);
                        type = "boolean";
                        break;
                    }
                case '!=':
                    {
                        newval = (left_expression_value?.value != right_expression_value?.value);
                        type = "boolean";
                        break;
                    }
                case '&&':
                    {
                        newval = (left_expression_value?.value && right_expression_value?.value);
                        type = "boolean";
                        break;
                    }
                case '||':
                    {
                        newval = (left_expression_value?.value || right_expression_value?.value);
                        type = "boolean";
                        break;
                    }
                default:
                    break;
            }

        } catch (error) {
            console.error(error);
            return get_default_failed_evaluation(++this.value_counter_ref.current);;
        }


        return {
            execution_result: "complete",
            value: {
                id: ++this.value_counter_ref.current,
                type,
                value: newval,
            }
        }
    }

}

export default ASTExecuter;

