import gsap from 'gsap';

type point = {
    x: number,
    y: number
}

function get_global_coordinate(element: Element): point {
    var rect = element.getBoundingClientRect(),
        scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
        scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    return { y: rect.top + scrollTop, x: rect.left + scrollLeft }
}

export default async function drag_element_to_another(starting_point_element: Element, dragged_element: Element, end_point_element: Element, speed: number) {
    return new Promise((resolve) => {

        const first_coord = get_global_coordinate(starting_point_element);
        const second_coord = get_global_coordinate(end_point_element);
        document.body.appendChild(dragged_element);
        const dehtml = dragged_element as HTMLElement
        dehtml.style.opacity = "0";
        dehtml.style.position = "absolute";
        const dragged_square = dragged_element.getBoundingClientRect();
        const first_square = starting_point_element.getBoundingClientRect();
        const second_square = end_point_element.getBoundingClientRect();
        const initial_x_position = first_coord.x;
        const initial_y_position = first_coord.y;
        const final_x_position = second_coord.x + (second_square.width - dragged_square.width) / 2;
        const final_y_position = second_coord.y + (second_square.height - dragged_square.height) / 2;
        dehtml.style.left = `${initial_x_position}px`;
        dehtml.style.top = `${initial_y_position}px`;
        const tl = gsap.timeline({ onComplete: resolve });
        tl.to(dragged_element, { duration: speed / 2000, opacity: 1 });
        tl.to(dragged_element, { duration: speed / 1000, left: final_x_position, top: final_y_position });

    })


}