import set_replacement from '../animation_effects/set_replacement';
import create_ast_expand_struct from '../animation_effects/create_ast_expand_struct';
import step_creator_type from './step_creator_type';
import { animation_state, value_update_info_type } from '../animation_state_types';
import restore_state from './restore_state';

const value_update_step_creator: step_creator_type = (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) => {

    const change = after.last_state_change as value_update_info_type;
    const change_details = change.details;

    async function animate(speed: number) {
        const val = change_details.expression_value;
        const value_elements_to_update = Array.from(scope_element.getElementsByClassName("scope-value-" + val.id));
        console.log({ value_elements_to_update }, "scope-value-" + val.id);
        const promises_array: Promise<any>[] = [];
        for (const el of value_elements_to_update) {
            // await set_replacement(code_element, ast_element, replacement_element, speed);
            const replacement = create_ast_expand_struct(val);
            console.log({ el });
            console.log({ "scope_parent:": el.parentElement });
            promises_array.push(set_replacement(scope_element, el, replacement.element, speed));
        }
        await Promise.all(
            promises_array
        );
    }

    async function rollback(speed: number) {
        restore_state(before, code_element, scope_element);
    }

    const step = {
        name: "value update",
        animate,
        rollback
    }

    return step;

}

export default value_update_step_creator;