import gsap from 'gsap';
import add_or_remove_class_to_text_containers from './add_or_remove_class_to_text_containers';
import highlight_element from './highlight_element';

export default function highlight_ast_object(code_container: Element, ast_id: number | null, speed: number) {
    // if (speed == 0) return;
    return new Promise(resolve => {
        const element = code_container.getElementsByClassName("ast-object-" + ast_id)[0];
        highlight_element(code_container, "lowlight");
        highlight_element(element);
        resolve();
        // setTimeout(resolve, speed);
    });
}