export default function create_scope(scope_container: Element) {
    const current_scope_classname = "current-scope";
    const current_scope = scope_container.getElementsByClassName(current_scope_classname)[0];
    const new_scope = document.createElement("div");
    new_scope.classList.add(current_scope_classname);
    new_scope.classList.add("scope");
    current_scope.appendChild(new_scope);
    current_scope.classList.remove(current_scope_classname);
    return new_scope;
}