import animation_step_type from './animation_step_type';

export default function merge_steps(steps: (animation_step_type | null)[]): animation_step_type | null {

    const non_null_steps = steps.filter(s => s);
    if (non_null_steps.length == 0) return null;
    const animate_functions: ((speed: number) => (Promise<any> | void))[] = [];
    const rollback_functions: ((speed: number) => (Promise<any> | void))[] = [];

    for (let i = 0; i < steps.length; i++) {
        const step = steps[i];
        if (step)
            animate_functions.push(step.animate);
    }
    for (let i = steps.length - 1; i >= 0; i--) {
        const step = steps[i];
        if (step)
            rollback_functions.push(step.rollback);
    }

    let returned_step: animation_step_type = {
        name: non_null_steps.map(s => s!.name).join(", "),
        animate: async function (speed: number) {
            for (let i = 0; i < animate_functions.length; i++) {
                const animate_f = animate_functions[i];
                await animate_f(speed);
            }
        },
        rollback: async function (speed: number) {
            for (let i = 0; i < rollback_functions.length; i++) {
                const rollback_f = rollback_functions[i];
                await rollback_f(speed);
            }
        }
    }

    return returned_step;
}
