import set_replacement from '../animation_effects/set_replacement';
import step_creator_type from './step_creator_type';
import { animation_state, for_loop_restoration_change_info_type } from '../animation_state_types';
import get_element_from_ast from './get_element_from_ast';
import scroll_to from '../animation_effects/scroll_to';


const for_loop_restoration_step_creator: step_creator_type = function (before: animation_state, after: animation_state, code_element: Element, scope_element: Element) {
    const change = after.last_state_change as for_loop_restoration_change_info_type;

    async function animate(speed: number) {
        const for_ast = change.details.for_ast;
        const for_element = code_element.getElementsByClassName("ast-object-" + for_ast.id)[0];
        const restored_for_element = get_element_from_ast(for_ast, {});
        await scroll_to(for_element, code_element, speed);
        await set_replacement(code_element, for_element, restored_for_element, speed);
    }

    async function rollback(speed: number) {
        // const ast_element = code_element.getElementsByClassName("ast-object-" + ast_id)[0];
        // let replacement_element = get_element_from_ast(ast_object, before.expression_value_replacements);
        // await scroll_to(ast_element,code_element,speed);
        // await set_replacement(code_element, ast_element, replacement_element, speed);
    }

    return {
        name: "evaluation replacement",
        animate,
        rollback
    }

}

export default for_loop_restoration_step_creator;